import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginGuard } from '../guards/login.guard';
import { CompleteInfoGuard } from '../guards/complete-info.guard';

//component
import { PsychologyHomeComponent } from "../psychology/psychology-home/psychology-home.component";
import { QuestionnaireComponent } from '../psychology/questionnaire/questionnaire.component';
import { PsychologyResultComponent } from "../psychology/psychology-result/psychology-result.component";


const routes: Routes = [

	{ path: '', component: PsychologyHomeComponent, canActivate:[CompleteInfoGuard] },
	{ path: 'category/:id/:parentId', component: PsychologyHomeComponent, canActivate:[CompleteInfoGuard] },

	{ path: 'questionnaire/:id', component: QuestionnaireComponent, canActivate:[CompleteInfoGuard] },
	{ path: 'result/:test_id/:answer_id', component: PsychologyResultComponent },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PsychologyRoutingModule { }
