import { CanActivate, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { TokenService } from '../auth/token.service';
import { Location } from '@angular/common'; //接收route para
import { catchError, map, tap } from 'rxjs/operators';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material'; //popup dialog
import { LoginComponent } from '../auth/login/login.component';
import { Injectable, Inject, PLATFORM_ID } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';

@Injectable({
	providedIn: 'root'
})
export class DiaryGuard implements CanActivate {
	constructor(@Inject(PLATFORM_ID) private platformId: Object, private tokenService:TokenService, private router:Router, private dialog:MatDialog) {}

	canActivate(
		next: ActivatedRouteSnapshot,
		state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {

		// BROWSER
   		if (isPlatformBrowser(this.platformId)) {

			return this.tokenService.infoState().pipe(
				map(code => {
					switch (code) {
						case "SUCCESS":
							return true;
							//break;

						case "BLOCK":
							console.warn(state.url);
			      			console.warn(next);
							this.router.navigate(['/member/fillcompleteinfo', state.url]);
			        		return false;
			        		//break;

			        	case "NO_SUBSCRIPTION":
			        		this.router.navigate(['/diary_cover']);
			        		return false;
			        		
						default:
							console.warn(state.url);
			      			console.warn(next);
							this.openDialog(state.url);
			        		return false;
							//break;
					}

		      		
	      		})
	      	);
		}
		
	}


	//開啟pop up
    openDialog(data){
    	const dialogRef = this.dialog.open(LoginComponent, {
    		maxWidth: '100%',
		  	width: '500px',
		  	data: { returnUrl: data}
		});
    }
}
