import { Component, OnInit, Input } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para

@Component({
	selector: 'app-google-search',
	templateUrl: './google-search.component.html',
	styleUrls: ['./google-search.component.scss']
})
export class GoogleSearchComponent implements OnInit {

	search;
	result;
	nextPage;
	list;

	//是否開啟read more div
	readmoreCheck = 0;
	nodata = 0;
	//一次要顯示幾筆
	limit = 10;
	moredata = 1;

	//input 顯示序號
	@Input() input_param;


	constructor(private httpService:HttpService, private route:ActivatedRoute) { }

	ngOnInit() {
		this.readmoreCheck = 0;
		if(!this.input_param){
			this.route.params.subscribe(params => {
    			
    			this.input_param = {
    				'start': 1,
    				'keyword': params["keyword"],
    			};
    			this.getGSearchResult(params["keyword"]);
    		});
		}else{
			this.getGSearchResult(this.input_param.keyword);
		}
	}


	//看更多
	more() {
		this.readmoreCheck = 1;
		this.input_param.start+=this.limit;	
	}


	getGSearchResult(keyword){
		let url = `https://www.googleapis.com/customsearch/v1?key=${Config.GoogleSearchKey}&cx=${Config.GoogleSearchID}&q=${keyword}&start=${this.input_param.start}`;

		this.httpService.getHttp(url).subscribe(
			res => {
				console.log(res);
				this.nextPage = null;
				this.list = null;
				this.result = res.searchInformation;

				if(this.result.totalResults=="0"){
					this.moredata = 0;
					this.nodata = 1;
				}

				if(res.queries.nextPage)
					this.nextPage = res.queries.nextPage[0].startIndex;

				if(res.items)
					this.list = res.items;
			}
		);
	}



}
