import { Component, OnInit, Input } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import swal, { SweetAlertOptions } from 'sweetalert2';
import { MemberService } from "../../member/member.service";
import { ProductService } from "../../services/product.service";
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material'; //popup dialog
import { LoginComponent } from '../../auth/login/login.component';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para

@Component({
	selector: 'app-buy-later',
	templateUrl: './buy-later.component.html',
	styleUrls: ['./buy-later.component.css']
})
export class BuyLaterComponent implements OnInit {

	//input 商品ID
	@Input() input_pId;
	@Input() add_check;
	@Input() add_status;
	@Input() product_quantity;
	product_number = 1;

	constructor(private productService:ProductService, private memberService:MemberService, private httpService:HttpService, private router:Router, private dialog:MatDialog) { }

	ngOnInit() {
	}


	//加入下次再買清單
	add() {

		if(this.product_number > this.product_quantity && this.add_check==true){
			swal({
				title: '庫存不夠!',
				text: '不可加入購物車',
				type: 'error',
			});
			return false;
		}

		//未登入 跳出登入畫面
		if(!this.memberService.user){
			this.login();
			return;
		}

		let cart = {};
		cart[`${this.input_pId}`] = this.product_number;
		let params:FormData = new FormData();
		params.append('products', JSON.stringify(cart));
		let url = `${Config.APIUrl}/api/shop/later-buy/merge`;

		this.httpService.postHttp(url, params).subscribe(
			res => {
			
				swal({
					title: '操作成功!',
					text: '已加入至下次購買清單',
					type: 'success',
				});
			}
		);
	}


	//直接購買
	buy() {

		if(this.product_number > this.product_quantity && this.add_check==true){
			swal({
				title: '庫存不夠!',
				text: '不可加入購物車',
				type: 'error',
			});
			return false;
		}

		//登入狀態
		if(this.memberService.user){
			console.log("login");
			let cart = {};
			cart[`${this.input_pId}`] = this.product_number;
			let params:FormData = new FormData();
			params.append('products', JSON.stringify(cart));
			let url = `${Config.APIUrl}/api/shop/cart/merge`;
			this.httpService.postHttp(url, params).subscribe(
				res => {
					//更新購物車數量
					this.productService.getCartNumber();
					
					//導向購物車葉面
					this.router.navigate(['/cart']);
				}
			);

		//未登入狀態
		}else{
			this.login();
			return;		

		}
	}


	login() {
		console.log(this.router.url);
		this.openDialog(this.router.url);
	}

	//開啟pop up
    openDialog(data){
    	const dialogRef = this.dialog.open(LoginComponent, {
    		maxWidth: '100%',
		  	width: '500px',
		  	data: { returnUrl: data}
		});
    }


    more(){
		this.product_number += 1;
	}

	less(){
		if(this.product_number >= 2){
			this.product_number -= 1;
		}
	}

}
