import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import  * as Config from '../../models/config.model';
import { HttpService } from '../../services/http.service';
import { Inject, PLATFORM_ID } from '@angular/core';
import { isPlatformBrowser } from '@angular/common';
import { Meta } from '@angular/platform-browser';
import { Chart } from 'chart.js';
import { SocialShareService } from '../../services/social-share.service';
import { DomSanitizer } from '@angular/platform-browser';


declare var window: any;

@Component({
	selector: 'app-psychology-result',
	templateUrl: './psychology-result.component.html',
	styleUrls: ['./psychology-result.component.css']
})
export class PsychologyResultComponent implements OnInit {

	chart;
	chart_type;
	score_sum;
	title;
	result;
	hashtags;
	ans_id;
	test_id;

	hashtag_show;
	chart_img = [];
	canvas;

	chart_data;

	constructor(private sanitizer: DomSanitizer, private shareService:SocialShareService, private meta: Meta, @Inject(PLATFORM_ID) private platformId: Object, private route: ActivatedRoute, private http:HttpService) { }

	ngOnInit() {

		this.route.params.subscribe(params => {
			
			this.ans_id = params['answer_id'];
			this.test_id = params['test_id'];
			this.getResultImage();

			if (isPlatformBrowser(this.platformId)) {
				window.ans_id = this.ans_id;
				window.http = this.http;
				window.meta = this.meta;

				window["uploadImg"] = function(file){
					let params:FormData = new FormData();
					params.append('id',window.ans_id);
			  		params.append('file',file);
			  		params.append('filename',`${window.ans_id}.png`);
			  		params.append('extension','png');

			  		let url = `${Config.APIUrl}/api/member/psychological-test-answer/upload-result-image`;

			  		//上傳圖片
			  		//this.http.loadingOpen = false;
					window.http.postHttp(url,params).subscribe(
						res => {

							/*
							console.log(res.filepath);
							//設定分享的內容縮圖
							window.meta.addTags([
							  	{ property: 'og:title', content: this.title },
							  	{ property: 'og:description', content: 'test' },
							  	{ property: 'og:image', content: `${Config.FileUrl}${res.filepath}` },
							]);
							*/
								
						}
					);
				};
			}


    		this.getChart(params['answer_id']);
    		this.getResult(params['test_id']);
    		this.checkAnsOwner();
    	});
	}



	ngOnDestroy(){
		if (isPlatformBrowser(this.platformId)) {
			delete window.ans_id;
			delete window.http;
			delete window.meta;
			delete window["uploadImg"];
		}
	}



	//判斷瀏覽者是不是作答者
	checkAnsOwner() {
		let url = `${Config.APIUrl}/api/member/psychological-test-answer/check-permission?id=${this.ans_id}`;
		this.http.getHttp(url).subscribe(
			res => {
				if(res.result == 1)
					this.getHashtag(this.ans_id);
			}
		);
	}


	//取得結果圖片 並 設定meta
	getResultImage(){
		let url = `${Config.APIUrl}/api/member/psychological-test-answer/result-image?id=${this.ans_id}`;
		this.http.getHttp(url).subscribe(
			res => {
				console.log(res);
				//設定分享的內容縮圖
				this.meta.addTags([
				  	{ property: 'og:title', content: res.title },
				  	{ property: 'og:description', content: res.description },
				  	{ property: 'og:image', content: `${Config.FileUrl}${res.image}` },
				]);
			}
		);
	}



	
	/*上傳CHART IMG 目前沒用到
	uploadImg(file){
		let params:FormData = new FormData();
  			params.append('file',file);
  			params.append('filename','p_test.png');
  			params.append('extension','png');

  			let url = `${Config.APIUrl}/api/upload/image/base64/p-test-answer`;

  			//上傳圖片
			this.http.postHttp(url,params).subscribe(
				res => {
					console.log(res.filepath);
					//設定分享的內容縮圖
					this.meta.addTags([
				  		{ property: 'og:title', content: this.title },
				  		{ property: 'og:description', content: 'test' },
				  		{ property: 'og:image', content: `${Config.FileUrl}${res.filepath}` },
				  	]);
					//this.updateChartImg(res.filepath);
				}
			);
	}
	*/


	//社群分享
	socialShare(type) {
		let url;
		switch (type) {
			case "line":
				url = `${Config.angularUrl}/psychology/result/${this.test_id}/${this.ans_id}`;
				this.shareService.lineShare(url);
				break;
			
			case "FB":
				url = `${Config.angularUrl}/psychology/result/${this.test_id}/${this.ans_id}`;
				this.shareService.FBShare(url);
				break;

			default:
				// code...
				break;
		}
	}
	


	//取得結果說明
	getResult(id){
		let url:string;

		url = `${Config.APIUrl}/api/content/psychological-test/show?id=${id}&viewed=0`;
		this.http.getHttp(url).subscribe(
			res => {
				this.title = res.test.title;
				this.result = this.sanitizer.bypassSecurityTrustHtml(res.test.result_content);
				this.chart_type = res.test.chart_setting;
				console.log(res);

				if (isPlatformBrowser(this.platformId)) {

					//加入心理測驗圖表function
					if(this.chart_type=="radar"){
						//進行chatjs 雷達圖
						this.setChart("radar");
					}

					if(this.chart_type=="polarArea"){
						//進行chatjs 扇形圖
						this.setChartPolarArea();
					}

					if(this.chart_type=="bar"){
						//進行chatjs 水平長條圖
						this.setChartBar();
					}

					if(this.chart_type=="score"){
						//分數
						this.setChartScore();
					}
				}

				//this.getResultImage();

			}
		);
	}


	//取得結果圖表data
	getChart(id){
		let url:string;

		url = `${Config.APIUrl}/api/member/psychological-test-answer/chart-data?id=${id}`;
		this.http.getHttp(url).subscribe(
			res => {

				this.chart = res;
				console.log(res);

			}
		);
	}


	//取得結果hashtag
	getHashtag(id){
		let url:string;

		url = `${Config.APIUrl}/api/member/psychological-test-answer/show?id=${id}`;
		this.http.getHttp(url).subscribe(
			res => {
				this.hashtags = res.hash_tags;
				console.log(res);
			}
		);
	}


	// 雷達圖
	setChartRadar(){
		this.chart_img = new Chart('chart', {
	    type: 'radar',
	    data: {
	    	labels: this.chart.labels,
				datasets: [{
					label: this.title,
					backgroundColor: 'rgba(222,186,168,0.5)',
					borderColor: 'rgb(222,186,168)',
					pointBackgroundColor: 'rgb(222,186,168)',
					data: this.chart.datas
				}]
			},
			options: {
				aspectRatio: 1.15,
				scale: {
					ticks: {
						min: parseInt(this.chart.ticks.min),
						max: parseInt(this.chart.ticks.max),
						// beginAtZero: true
					}
				},
				animation : {
					onComplete: function (animation) { 
						this.options.animation.onComplete = null; //disable after first rende
						console.log(this.toBase64Image()); 
						window["uploadImg"](this.toBase64Image());
						
					}
	        		
				}
			}
		});
	}


	// 扇形圖
	setChartPolarArea(){
		this.chart_img = new Chart('chart', {
			type: 'polarArea',
	    data: {
				labels: this.chart.labels,
				datasets: [{
					label: this.title, // for legend
					data: this.chart.datas,
					backgroundColor: [
						'rgba(246,236,230,0.5)',
						'rgba(222,186,168,0.5)',
						'rgba(133,101,109,0.5)',
						'rgba(62,51,66,0.5)',
						// 重複四色
						'rgba(246,236,230,0.5)',
						'rgba(222,186,168,0.5)',
						'rgba(133,101,109,0.5)',
						'rgba(62,51,66,0.5)',
					],
				}],
			},
			options: {
				aspectRatio: 0.99,
				responsive: true,
				legend: {
					position: 'top',
				},
				scale: {
					ticks: {
						min: parseInt(this.chart.ticks.min),
						max: parseInt(this.chart.ticks.max),
						// beginAtZero: true
					},
					reverse: false
				},
				animation: {
					animateRotate: false,
					animateScale: true,
					onComplete: function (animation) { 
						this.options.animation.onComplete = null; //disable after first rende
						console.log(animation); 
						console.log(this.toBase64Image()); 
						window["uploadImg"](this.toBase64Image());
						
					}
				},
				
			}
		});
	}


	// 水平長條圖
	setChartBar(){

		this.chart_img = new Chart('chart', {
			type: 'horizontalBar',
			data: {
				labels: this.chart.labels,
				datasets: [{
					label: this.title,
					backgroundColor: [
						'rgba(246,236,230,0.5)',
						'rgba(222,186,168,0.5)',
						'rgba(133,101,109,0.5)',
						'rgba(62,51,66,0.5)',
						// 重複四色
						'rgba(246,236,230,0.5)',
						'rgba(222,186,168,0.5)',
						'rgba(133,101,109,0.5)',
						'rgba(62,51,66,0.5)',
					],
					borderColor: [
						'rgba(246,236,230,1)',
						'rgba(222,186,168,1)',
						'rgba(133,101,109,1)',
						'rgba(62,51,66,1)',
						// 重複四色
						'rgba(246,236,230,1)',
						'rgba(222,186,168,1)',
						'rgba(133,101,109,1)',
						'rgba(62,51,66,1)',
					],
					borderWidth: 1,
					data: this.chart.datas,
				}]
			},
			options: {
				aspectRatio: 1.8,
				responsive: true,
				legend: {
					position: 'top',
				},
				scales: {
				  xAxes: [{
				    ticks: {
				      min: parseInt(this.chart.ticks.min),
				      max: parseInt(this.chart.ticks.max),
				    }
				  }]
				},
				animation : {
					onComplete: function (animation) { 
						this.options.animation.onComplete = null; //disable after first rende
						console.log(this.toBase64Image()); 
						window["uploadImg"](this.toBase64Image());
						
					}
	        		
				}
				
			}
		});

	}
	


	// 分數
	setChartScore(){
		const scores = this.chart.datas;
		const reducer = (accumulator, currentValue) => accumulator + currentValue;
		
		this.score_sum = scores.reduce(reducer);
	}


	// 設定chartjs圖表
	setChart(type){
		// 雷達圖
		if(type=="radar")
			this.setChartRadar();

		// 扇形圖
		if(type=="polarArea")
			this.setChartPolarArea();

		// 水平長條圖
		if(type=="bar")
			this.setChartBar();

		// 分數
		if(type=="score")
			this.setChartScore();
	}

}
