import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import { formatDate } from '@angular/common';

declare var Chart: any;

@Component({
	selector: 'app-diary-chart',
	templateUrl: './diary-chart.component.html',
	styleUrls: ['./diary-chart.component.scss']
})
export class DiaryChartComponent implements OnInit {

	//input 
	@Input() inputDates;
	chart_line:any = {};
	chart_bubble:any = {};

	constructor(private httpService:HttpService) { }

	ngOnInit() {
		if(this.inputDates){
			this.getChartData('line','feeling');
			this.getChartData('line','excitement');
			this.getChartData('bubble','feeling');
			//this.getChartData('bubble','excitement');

		}
	}

	

	//如果input進來的參數改變, 重新取得心理測驗列表
	ngOnChanges(changes: SimpleChanges) {
		console.log(changes);
    	this.inputDates = changes.inputDates.currentValue;
    	this.getChartData('line','feeling');
		this.getChartData('line','excitement');
		this.getChartData('bubble','feeling');
		//this.getChartData('bubble','excitement');
	}


	//獲得圖表數據
	getChartData(chart_type, score_type){
		let url:string;

		const paramObject = {
			'start_date': formatDate(this.inputDates.start, 'yyyy-MM-dd', 'en-US'),
			'end_date': formatDate(this.inputDates.end, 'yyyy-MM-dd', 'en-US'),
			'chart_type': chart_type,
			'score_type': score_type,
		};
		//console.log(paramObject);

		let param = this.httpService.getParamFormat(paramObject);
		url = `${Config.APIUrl}/api/member/diary/feeling-record?${param}`;

		this.httpService.getHttp(url).subscribe(
			res =>{

				//折線圖
				if(chart_type=="line"){
					this.chart_line[score_type] = res;

					if(score_type=="feeling"){
						//進行chatjs 心情折線圖
						this.setChart("feelingLine");

					}else if(score_type=="excitement"){
						//進行chatjs 激動折線圖
						this.setChart("excitingLine");
					}
				}

				//泡泡圖
				if(chart_type=="bubble"){
					this.chart_bubble[score_type] = res;

					if(score_type=="feeling"){
						//進行chatjs 心情泡泡圖
						this.setChart("feelingBubble");
					}
				}

			}
		);
	}


	// 心情圖譜泡泡圖
	setChartFeelingBubble(){
		new Chart('chart-bubble', {
		  type: 'bubble',
		  data: {
		    datasets: [
		      {
		        label: '前測',
		        backgroundColor: 'rgba(117, 194, 153, 0.7)',
		        borderColor: 'rgba(117, 194, 153, 0)',
		        data: this.chart_bubble.feeling.pre_datas,
		        /*
		        data: [
		          {
		            date: '10/4',
		            x: 3,
		            y: 7,
		            v: 100
		          },
		          {
		            date: '11/24',
		            x: 8.2,
		            y: 7.6,
		            v: 100
		          }
		        ],
		        */
		      }, {
		        label: '後測',
		        backgroundColor: 'rgba(163, 194, 117, 0.7)',
		        borderColor: 'rgba(163, 194, 117, 0)',
		        data: this.chart_bubble.feeling.post_datas,
		        /*
		        data: [
		          {
		            date: '10/4',
		            x: 8.1,
		            y: 7.4,
		            v: 100
		          },
		          {
		            date: '11/24',
		            x: 8,
		            y: 7,
		            v: 100
		          }
		        ],
		        */
		      },
		    ]
		  },
		  options: {
		  	maintainAspectRatio: false,
		  	// aspectRatio: 1.5,
		    elements: {
		      point: {
		        radius: function(context) {
		          let value = context.dataset.data[context.dataIndex];
		          let size = context.chart.width;
		          let base = Math.abs(value.v) / 300;
		          return (size / 24) * base;
		        }
		      }
		    },
		    scales: {
		      xAxes: [{
		        position: "bottom",
		        gridLines: {
		          color: 'lightgray',
		          zeroLineColor: '#3c4f25'
		        },
		        scaleLabel: {
		          display: true,
		          labelString: '不激動'
		        },
		        ticks: {
		          min: -5,
		          max: 5,
		          stepSize: 1
		        }
		      },{
		        position: "top",
		        gridLines: {
		          display: false,
		        },
		        scaleLabel: {
		          display: true,
		          labelString: '激動'
		        },
		        ticks: {
		          min: -5,
		          max: 5,
		          stepSize: 1
		        }
		      }],
		      yAxes: [{
		        gridLines: {
		          color: 'lightgray',
		          zeroLineColor: '#3c4f25'
		        },
		        scaleLabel: {
		          display: true,
		          labelString: '不開心'
		        },
		        ticks: {
		          min: -5,
		          max: 5,
		          stepSize: 1
		        }
		      },{
		        position: "right",
		        gridLines: {
		          display: false,
		        },
		        scaleLabel: {
		          display: true,
		          labelString: '開心'
		        },
		        ticks: {
		          min: -5,
		          max: 5,
		          stepSize: 1
		        }
		      }]
		    },
		    title: {
		      text: '您的情緒圖譜'
		    },
		    plugins: {
		      datalabels: {
		        color: function(context) {
		          return 'white';
		        },
		        font: {
		          weight: 'bold'
		        },
		        formatter: function(value, context) {
		          return value.date
		        },
		      }
		    }
		  }
		});
	}


	// 激動程度折線圖
	setChartExcitingLine(){
		new Chart('chart-line-excitement', {
	    type: 'line',
	    data: {
	    		labels: this.chart_line.excitement.labels,
				//labels: [ "2018-11-05", "2018-11-08", "2018-11-16", "2018-11-09" ],
				datasets: [
					{
						label: '前測',
						backgroundColor: 'rgba(117, 194, 153, 0.7)',
						borderColor: 'rgb(117, 194, 153)',
						data: this.chart_line.excitement.pre_datas,
						//data: [ "7", "6", "3", "6" ],
						fill: false,
						pointRadius: 4,
					},
					{
						label: '後測',
						backgroundColor: 'rgba(163, 194, 117, 0.7)',
						borderColor: 'rgb(163, 194, 117)',
						data: this.chart_line.excitement.post_datas,
						//data: [ "1", "3", "4", "7" ],
						fill: false,
						pointRadius: 4,
					},
				]
			},
			options: {
				aspectRatio: 1.1,
			  elements: {
  				line: {
  					tension: 0
  				}
  			},
			  scales: {
			  	xAxes: [{
			  		position: "bottom",
			  		scaleLabel: {
			  		  display: false,
			  		},
			  		ticks: {
			  		  autoSkip: false
			  		}
			  	},{
			  	  position: "top",
			  	  scaleLabel: {
			  	    display: true,
			  	    labelString: '激動程度，分數越高代表越激動',
			  	    fontSize: 16,
			  	    padding: 0
			  	  },
			  	  labels: []
			  	}],
			    yAxes: [{
			      ticks: {
			        min: -5,
		          max: 5,
			        stepSize: 1
			      }
			    }]
			  },
			  title: {
			    text: '您的激動程度'
			  },
			  plugins: {
			    datalabels: {
			      display: function(context) {
		          return false
			      }
			    }
			  }
			}
		});

	}


	// 心情程度折線圖
	setChartFeelingLine(){
		new Chart('chart-line-feeling', {
	    type: 'line',
	    data: {
	    	labels: this.chart_line.feeling.labels,
				//labels: [ "2018-11-05", "2018-11-08", "2018-11-16", "2018-11-09" ],
				datasets: [
					{
						label: '前測',
						backgroundColor: 'rgba(117, 194, 153, 0.7)',
						borderColor: 'rgb(117, 194, 153)',
						data: this.chart_line.feeling.pre_datas,
						//data: [ "7", "6", "3", "6" ],
						fill: false,
						pointRadius: 4,
					},
					{
						label: '後測',
						backgroundColor: 'rgba(163, 194, 117, 0.7)',
						borderColor: 'rgb(163, 194, 117)',
						data: this.chart_line.feeling.post_datas,
						//data: [ "1", "3", "4", "7" ],
						fill: false,
						pointRadius: 4,
					},
				]
			},
			options: {
				aspectRatio: 1.1,
			  elements: {
  				line: {
  					tension: 0
  				}
  			},
			  scales: {
			  	xAxes: [{
			  	  position: "bottom",
			  	  scaleLabel: {
			  	    display: false,
			  	  },
			  	  ticks: {
			  	    autoSkip: false
			  	  }
			  	},{
			  		position: "top",
			  		scaleLabel: {
			  		  display: true,
			  		  labelString: '心情程度，分數越高代表心情越好',
			  		  fontSize: 16,
			  		  padding: 0
			  		},
			  		labels: []
			  	}],
			    yAxes: [{
			      ticks: {
			        min: -5,
		          max: 5,
			        stepSize: 1
			      }
			    }]
			  },
			  title: {
			    text: '您的心情程度'
			  },
			  plugins: {
			    datalabels: {
			      display: function(context) {
		          return false
			      }
			    }
			  }
			}
		});
	}


	// 設定chartjs圖表
	setChart(type){
		// 圖表共同設定
		Chart.helpers.merge(Chart.defaults.global, {
			legend: {
			  position: 'bottom',
			},
		  tooltips: false,
		  title: {
		  	display: true,
		    fontSize: 22
		  },
		});

		// 心情圖譜泡泡圖
		if(type=="feelingBubble")
			this.setChartFeelingBubble();

		// 激動程度折線圖
		if(type=="excitingLine")
			this.setChartExcitingLine();

		// 心情程度折線圖
		if(type=="feelingLine")
			this.setChartFeelingLine();
	}


}
