import { Component, OnInit, Input } from '@angular/core';
import { ProductService } from '../../services/product.service';
import { CookieService } from '../../services/cookie.service';

@Component({
	selector: 'app-discount-code',
	templateUrl: './discount-code.component.html',
	styleUrls: ['./discount-code.component.scss']
})
export class DiscountCodeComponent implements OnInit {

	code;
	@Input() hide;

	constructor(public productService:ProductService, private cookieService:CookieService) { }

	ngOnInit() {
		this.code = this.cookieService.getCookie('discount_code');
		this.send();
	}


	send(){
		console.log(this.code);
		this.productService.getDiscountCode(this.code);	
	}


	cancel(){
		this.code = null;
		this.productService.discount_code = null;
		this.productService.discount_info = null;
		this.cookieService.deleteCookie('discount_code');
	}

}
