import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import swal, { SweetAlertOptions } from 'sweetalert2';

@Component({
	selector: 'app-subscription-record',
	templateUrl: './subscription-record.component.html',
	styleUrls: ['./subscription-record.component.scss']
})
export class SubscriptionRecordComponent implements OnInit {

	list;
	detail_open:any = [];
	payUrl = `${Config.APIUrl}/api/shop/period-payment/go-pay`;

	constructor(private httpService:HttpService) { }

	ngOnInit() {
		this.getSubscriptionList();
	}


	//訂閱列表
	getSubscriptionList() {
		let url = `${Config.APIUrl}/api/member/subscription`;

		this.httpService.getHttp(url).subscribe(
			res => this.list = res.subscriptions
		);
	}


	//取消訂閱
	cancelSubscription(id){
		let url = `${Config.APIUrl}/api/member/subscription/cancel`;
		let params:FormData = new FormData();
		params.append('id',id);
		this.httpService.postHttp(url, params).subscribe(
			res => this.getSubscriptionList()
		);

	}


	//取消確認
	cancelConfirm(id){
		let url = `${Config.APIUrl}/api/content/parameter?name=subscription_cancel_description`;
		this.httpService.getHttp(url).subscribe(
			res => {
				swal({
					title: '取消訂閱',
					html: res.value,
					confirmButtonText:  '取消訂閱',
  					cancelButtonText:  '繼續訂閱',
					showCancelButton:true,
					focusCancel:true,
							
				}).then(
					(success) => 
				{
					if(success.value)
						this.cancelSubscription(id);
				});
			}
		);
		
		
	}





	//開啟 關閉訂單明細
	openDetail(id){
		if(this.detail_open[id])
			delete this.detail_open[id];
		else
			this.detail_open[id] = true;
	}


}
