import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import swal, { SweetAlertOptions } from 'sweetalert2';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material'; //popup dialog
import { DiaryCreateComponent } from "../diary-create/diary-create.component";
import { DiaryService } from "../diary.service";


@Component({
	selector: 'app-diary-home',
	templateUrl: './diary-home.component.html',
	styleUrls: ['./diary-home.component.css']
})
export class DiaryHomeComponent implements OnInit {

	date = new Date().getTime();

	background;
	fileUrl = Config.FileUrl;

	constructor(public diaryService:DiaryService, public dialog: MatDialog, private router:Router, private route:ActivatedRoute, private httpService:HttpService) { }

	ngOnInit() {
		//this.getDiaryDates();
		this.diaryService.getDiaryDates();
		this.uiControl();
		this.getBackground();
	}


	//取得日記背景
	getBackground(){
		let url = `${Config.APIUrl}/api/member/diary-background/default`;
		this.httpService.getHttp(url).subscribe(
				res => this.background = res.filepath
		);
	}


	//[calendar] Child模板事件發生時，會呼叫此方法回傳值
	onListenChild($event: any) {
	    console.log($event.date);
	    console.log($event.events);
	    //如果有寫過日記
	    if($event.events.length){
	    	swal({
	    		title: "確認修改？",
			  	text: "確定修改後，需重新做情緒測量",
			  	type: "question",
			  	showCancelButton:true,
			  	focusCancel:true,
					
			}).then(
				(success) => {
					console.log(success);
					if(success.value){
						this.router.navigate(['edit', $event.events[0].id],  { relativeTo: this.route });
						
					}
	    	});

	    //沒寫過日記	去新增日記頁面
	    }else{
	    	console.log("沒寫日記");

	    	//如果日期超過今天 不能填
	    	let today = new Date().getTime();
	    	if($event.date.getTime() > today){
	    		swal({
					title: '操作錯誤',
					text: '你不可以新增未來日記',
					type: 'error',
				})
	    	}else{
	    		let date = $event.date.getTime();
	    		this.openDialog(date);
	    		//this.router.navigate(['create', date],  { relativeTo: this.route });
	    		
	    	}	
	    }
	}


	gotoCreate(){
		this.openDialog(this.date);
	}


	//開啟pop up
	openDialog(data){
  	const dialogRef = this.dialog.open(DiaryCreateComponent, {
  		maxWidth: '100%',
	  	width: '600px',
	  	data: {date: data}
		});
	}

	// ui jquery
	uiControl(){
		// 手機選單
		$('.diary-toggler').click(function(){
			$('.diary-side').toggleClass('open');
		})
		$('.diary-menu-link').click(function(){
			$('.diary-side').removeClass('open');
		})
		// 新增日記後關閉選單
		$('body').on('click', '.swal2-actions', function(){
			$('.diary-side').removeClass('open');
		})
	}


}
