import { Component, OnInit, Output, EventEmitter, Input, ElementRef, ViewChild  } from '@angular/core';
import { NgForm, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { HttpService } from '../../services/http.service';
import swal, { SweetAlertOptions } from 'sweetalert2';
import  * as Config from '../../models/config.model';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import * as $ from 'jquery';
import { CropperOption } from 'ngx-cropper';

@Component({
	selector: 'app-file-upload',
  	templateUrl: './file-upload.component.html',
  	styleUrls: ['./file-upload.component.css']
})


export class FileUploadComponent implements OnInit {

	public cropperConfig: CropperOption;
	@Input() inputImageUpload;	

	//是否剪裁
	@Input() crop = false;
	//Output event to parent
	@Output() outputFileEvent = new EventEmitter<any>(); //提供Output裝飾器，讓該事件成為父親模板的事件

  	sendChild(eventdata) {
    	this.outputFileEvent.emit(eventdata);
  	}

  	
	uploadForm = this.formBuilder.group({
		file: null,
	});

	type = 'avatar';
	width = '0';
	img_url = null;
	video_url = null;
	submitted = false;

	//這裡控制要檢查的項目，true表示要檢查，false表示不檢查 
	isCheckImageType = false;  //是否檢查圖片副檔名 
	isCheckFileSize = true;  //是否檢查圖片檔案
	isCheckVideoType = true; //是否檢查video副檔名 
	isCheckVideoTime = true; //是否檢查video時間 




  	constructor(private httpService:HttpService, private router:Router, private formBuilder: FormBuilder) { }
	
  	ngOnInit() {

  		this.cropperConfig = {
		    url: null, // image server url
		    maxsize: Config.UploadSize, // image max size, default 500k = 512000bit
		    title: '選取圖片要顯示的區塊', // edit modal title, this is default
		    uploadBtnName: '選擇圖片', // default Upload Image
		    uploadBtnClass: 'btn btn-block', // default bootstrap styles, btn btn-primary
		    cancelBtnName: '取消', // default Cancel
		    cancelBtnClass: null, // default bootstrap styles, btn btn-default
		    applyBtnName: '確定', // default Apply
		    applyBtnClass: null, // default bootstrap styles, btn btn-primary
		    errorMsgs: {  // These error msgs are to be displayed to the user (not the ones sent in returnData)
		      4000: null, // default `Max size allowed is ${maxsize}kb, current size is ${currentSize}kb`
		      4001: null  // default 'When sent to the server, something went wrong'
		    },
		    fdName: 'file', // default 'file', this is  Content-Disposition: form-data; name="file"; filename="fire.jpg"
		    //fdName: ,
		    aspectRatio: 1 / 1, // default 1 / 1, for example: 16 / 9, 4 / 3 ...
		    viewMode: 0 // default 0, value can be 0, 1, 2, 3
    	};
  		
  		if(this.inputImageUpload){
  			console.log(this.inputImageUpload);
	  		this.isCheckImageType = this.inputImageUpload.isCheckImageType;
	  		this.isCheckFileSize = this.inputImageUpload.isCheckFileSize;
			this.isCheckVideoType = this.inputImageUpload.isCheckVideoType; 
			this.isCheckVideoTime = this.inputImageUpload.isCheckVideoTime;
			this.width = this.inputImageUpload.width;
			if(this.inputImageUpload.type)
				this.type = this.inputImageUpload.type;
		}
  	}

  	onFileChange(event) {
	    if(event.target.files.length > 0) {
	      	let file = event.target.files[0]; //取得元素的file欄位
	      	this.uploadForm.get('file').setValue(file);
	      	$('#submit_file').prop('disabled', false);
	      	$('.custom-file-label').text(file.name); //圖片檔名顯示於label
	      	console.log(file);

	      	//檢查檔案格式
	      	this.checkFileType(file);
	      	//檢查檔案大小
	      	this.checkFileSize(file);
	      	//檢查影片時間
	      	this.checkVideoTime(file)
	    }
  	}


  	//檢查檔案格式
  	checkFileType(file){
  		let allowType = ['image/jpeg','image/png'];  //允許的圖片副檔名 
  		if (this.isCheckImageType && allowType.indexOf(file.type)==-1) { 
  			this.img_url = null;
  			$('#submit_file').prop('disabled', true);
			swal({
				title: '檔案格式不符',
				text: '只允許jpg,png',
				type: 'error',
				confirmButtonText: '確認'
			});
		} 

		let allowType_video = ['video/mp4'];  //允許的影片副檔名 
  		if (this.isCheckVideoType && allowType_video.indexOf(file.type)==-1) { 
  			$('#submit_file').prop('disabled', true);
  			this.video_url = null;
			swal({
				title: '檔案格式不符',
				text: '只允許mp4',
				type: 'error',
				confirmButtonText: '確認'
			});
		} 
  	}


  	//檢查檔案大小
  	checkFileSize(file){
  		if (this.isCheckFileSize && file.size>Config.UploadSize) { 
  			this.video_url = null;
  			this.img_url = null;
  			$('#submit_file').prop('disabled', true);
			swal({
				title: '檔案大小不符',
				text: `需小於${Config.UploadSize}btyes`,
				type: 'error',
				confirmButtonText: '確認'
			});
		}
  	}


  	//檢查影片秒數
  	checkVideoTime(file){
  		if(this.isCheckVideoTime){
  			this.video_url = null;
	  		const objectUrl = URL.createObjectURL(file);
	  		$("#myVideo").prop("src", objectUrl); //指定audio來源
	  		$("#myVideo").on("canplaythrough", function(e){
	  			let video_element = $("#myVideo")[0] as HTMLMediaElement;
	  			let v_durtation:number  = video_element.duration;
	  			if(v_durtation>Config.UploadVideoTime){
	  				$('#submit_file').prop('disabled', true);
	  				swal({
						title: '影片長度過長',
						text: `需小於${Config.UploadVideoTime}秒`,
						type: 'error',
						confirmButtonText: '確認'
					});
	  			}
	  		});
  		
  		}
		console.log(this.submitted);
  	}


  	


  	//submit form
	onSubmit() {

  		let params:FormData = new FormData();
  		let url:string;
  		params.append('file', this.uploadForm.get('file').value);
  		  		
  		if(this.isCheckImageType==true){
  			url = `${Config.APIUrl}/api/upload/image/file`;
  			params.append('type',this.type);
  			params.append('width',this.width);
  		}else if(this.isCheckVideoType==true){
  			url = `${Config.APIUrl}/api/upload/video/file`;
  		}

	  	this.httpService.postHttp(url,params)
		    .subscribe(res => {

		    	if(this.isCheckImageType==true){
		    		this.img_url = `${Config.FileUrl}${res.filepath}`;
		    	}else if(this.isCheckVideoType==true){
		    		this.video_url = `${Config.FileUrl}${res.filepath}`;
		    	}
		    	//傳送資料給母component
		    	this.sendChild(res.filepath);
		    });
	}



	public onReturnData(data: any) {
    	// Do what you want to do
    	console.warn(data);
    	let params:FormData = new FormData();
  		let url:string;
    	url = `${Config.APIUrl}/api/upload/image/file`;
    	params.append('file', data);
  		params.append('type',this.type);
  		params.append('width',this.width);

  		this.httpService.postHttp(url,params)
		    .subscribe(res => {

		    	if(this.isCheckImageType==true){
		    		this.img_url = `${Config.FileUrl}${res.filepath}`;
		    	}else if(this.isCheckVideoType==true){
		    		this.video_url = `${Config.FileUrl}${res.filepath}`;
		    	}

		    	console.log(res);
		    	//傳送資料給母component
		    	this.sendChild(res.filepath);
		    });
	}



}
