import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import { HttpService } from '../services/http.service';
import  * as Config from '../models/config.model';


@Component({
	selector: 'app-page',
	templateUrl: './page.component.html',
	styleUrls: ['./page.component.scss']
})
export class PageComponent implements OnInit {

	page;

	constructor(private route:ActivatedRoute, private httpService:HttpService, private router:Router) { }

	ngOnInit() {
		this.route.params.subscribe(params => {
				this.getPage(params['code']);
			});
	}


	//取得客至頁面內容
	getPage(code){
		let url = `${Config.APIUrl}/api/content/page?website_id=1&code=${code}`;
		this.httpService.getHttp(url).subscribe(
				res =>{
					if(res.page.status==1)
						this.page = res.page;
					else
						this.router.navigate(['error404']);
					

				}
			);
	}



}
