import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';
import * as Config from '../../models/config.model';

@Component({
	selector: 'app-diary-background-home',
	templateUrl: './diary-background-home.component.html',
	styleUrls: ['./diary-background-home.component.scss']
})
export class DiaryBackgroundHomeComponent implements OnInit {

	input_param;
	image;
	img_url;
	title;
	error_show;


	//傳給FILE UPLOAD的參數
	inputImageUpload = {
		isCheckImageType: true, //是否檢查圖片副檔名 
		isCheckFileSize: true,  //是否檢查圖片檔案
		isCheckVideoType: false,//是否檢查video副檔名 
		isCheckVideoTime: false,//是否檢查video時間 
		width: 0,
		type: 'background',
	};

	// scrollbar套件設定
	public scrollbarOptions = { axis: 'y', theme: 'minimal-dark' };

	constructor(private httpService:HttpService) { }

	ngOnInit() {
		this.input_param = {
			'from' : 0,
		};
		
		this.onResize();
	}


	//上傳背景圖
	uploadBackground(){
		if(this.image && this.title){
			let params:FormData = new FormData();
  			params.append('title', this.title);
  			params.append('filepath', this.image);
  			params.append('default_image', '0');
			let url = `${Config.APIUrl}/api/member/diary-background`;

			this.httpService.postHttp(url, params).subscribe(
				res => {
					this.input_param = {
						'from' : 0,
					};
					this.error_show = false;
				}
			);

		}else{
			this.error_show = true;
		}
	}


	//[fileupload] Child模板事件發生時，會呼叫此方法回傳值
	onListenFile($event: any) {
		console.log($event);
		this.image = $event;
		this.img_url = null;
	}


	onResize(){
		// 當視窗寬度小於768時 移除捲軸
		$(window).resize(function(){
      if($(this).width()>768){
        $('.diary-scroll-container').mCustomScrollbar({ axis: 'y', theme: 'minimal-dark' }); //apply scrollbar with your options 
      }else{
        $('.diary-scroll-container').mCustomScrollbar("destroy"); //destroy scrollbar 
      }
    }).trigger("resize");
	}

}
