import { Component, OnInit, Output, EventEmitter, Input, SimpleChanges } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import * as $ from 'jquery';
// SSR
import { isPlatformBrowser } from '@angular/common';
import { Injectable, Inject, PLATFORM_ID } from '@angular/core';

@Component({
	selector: 'app-category',
	templateUrl: './category.component.html',
	styleUrls: ['./category.component.scss']
})
export class CategoryComponent implements OnInit {

	categories;
	@Output() childEvent = new EventEmitter<any>(); //提供Output裝飾器，讓該事件成為父親模板的事件
	@Input() inputId;

	constructor(@Inject(PLATFORM_ID) private platformId: Object, private http:HttpService, private router:Router, private route:ActivatedRoute) { }

	ngOnInit() {
		console.log(this.inputId);
		this.getCategoryList();
		this.uiControl();
	
	}

	//如果input進來的參數改變, 重新取得心理測驗列表
	ngOnChanges(changes: SimpleChanges) {
		
    	this.inputId = changes.inputId.currentValue;
    	console.log(this.inputId);
    	this.getCategoryList();
		this.uiControl();

	}


	//output id
	onClicked($event) {
		console.log($event);
		//output data
		this.childEvent.emit($event);
	}


	//取得類別列表
	getCategoryList() {
		let url:string;

		const paramObject = {
			'website_id': 1,
			'parent_id': 0,
		};
		//console.log(paramObject);

		let param = this.http.getParamFormat(paramObject);
		url = `${Config.APIUrl}/api/content/category?${param}`;
		this.http.getHttp(url).subscribe(
			res => {
				this.categories = res.categories;
				console.log(res);
			}
		);

	}


	// ui jquery
	uiControl(){
		// 主類別
		 // BROWSER
   		if (isPlatformBrowser(this.platformId)) {
		    $('body').on('click', '.category', function(){
		    	let index = $(this).index();
		    	$('.sub-categories').removeClass('d-flex');
		    	$('.sub-categories').eq(index).addClass('d-flex');

		    	$('.category').removeClass('active');
		    	$('.sub-category').removeClass('active');
		    	$(this).addClass('active');
		    })

		    // 子類別
		    $('body').on('click', '.sub-category', function(){
		    	$('.sub-category').removeClass('active');
		    	$(this).addClass('active');
		    })
		}
	}

}
