import { Component, OnInit } from '@angular/core';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para

@Component({
  selector: 'app-order-home',
  templateUrl: './order-home.component.html',
  styleUrls: ['./order-home.component.scss']
})
export class OrderHomeComponent implements OnInit {

	subscription;
	product;

  constructor(private router:Router) { }

  ngOnInit() {
  	if(this.router.url === '/member/subscription')
  		this.openSubscription();
  
  	if(this.router.url === '/member/order-record')
  		this.openProduct();
  }


  openSubscription(){
  	this.subscription = true;
  	this.product = false;
  }


  openProduct(){
  	this.subscription = false;
  	this.product = true;
  }

}
