import { Component, EventEmitter, Input, OnInit, Output, SimpleChanges  } from '@angular/core';
import {NgbDateStruct, NgbCalendar} from '@ng-bootstrap/ng-bootstrap';
import { formatDate } from '@angular/common';
import { NgForm, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para

@Component({
	selector: 'app-mydiary-search',
	templateUrl: './mydiary-search.component.html',
	styleUrls: ['./mydiary-search.component.css']
})
export class MydiarySearchComponent implements OnInit {

	//傳送給mydiary component 的參數
	inputMydiary;
	//timestamp 用來傳到網址的參數
	startdate;
	enddate;
	//判斷是否送出
	submitted = false;


	Form = this.formBuilder.group({
		start: ['',[Validators.required]],
		end: ['',[Validators.required]],
	});

	// convenience getter for easy access to form fields
	get start() { return this.Form.get('start'); }
	get end() { return this.Form.get('end'); }

	// scrollbar套件設定
	public scrollbarOptions = { axis: 'y', theme: 'minimal-dark' };

	constructor(private formBuilder:FormBuilder, private router:Router, private route:ActivatedRoute) { }


	ngOnInit() {

		this.route.params.subscribe(params => {

			//如果網址有時間參數 input 參數至 mydiary component
			if(params['start']){
				this.startdate = params['start'];
				this.enddate = params['end'];

			  	this.inputMydiary = {
			  		'startdate': formatDate(this.startdate, 'yyyy-MM-dd', 'en-US'),
			  		'enddate': formatDate(this.enddate, 'yyyy-MM-dd', 'en-US'),
			  		'from':0,
			  	};

			//如果沒有 自訂初始搜尋條件 input 參數至 mydiary component
			} else {
				let today = new Date();
				let lastMon = new Date();
				lastMon.setDate (lastMon.getDate() - 30);
			  	this.inputMydiary = {
					'startdate': formatDate(lastMon, 'yyyy-MM-dd', 'en-US'),
					'enddate': formatDate(today, 'yyyy-MM-dd', 'en-US'),
					'from': 0
				};
				this.enddate = today.getTime();
				this.startdate = lastMon.getTime();
			}

		});	

		this.onResize();

	}


	//[datepicker] Child模板事件發生時，會呼叫此方法回傳值
	onListenStart($event: any) {
	    let strFormChild = $event as Date;
	    this.startdate = strFormChild.getTime();
	}


	//[datepicker] Child模板事件發生時，會呼叫此方法回傳值
	onListenEnd($event: any) {
	    let strFormChild = $event as Date;
	    this.enddate = strFormChild.getTime();
	}



	//submit form
	onSubmit() {

		//判斷是否已按了送出 按了就要顯示驗證訊息
		this.submitted = true;

		//表單沒有驗證成功 沒有動作
		if(this.Form.invalid || (this.startdate > this.enddate)){
			return;
		}

  		
  		console.log(this.startdate);
  		console.log(this.enddate);

  		this.inputMydiary = {
			'startdate': formatDate(this.startdate, 'yyyy-MM-dd', 'en-US'),
			'enddate': formatDate(this.enddate, 'yyyy-MM-dd', 'en-US'),
			'from':0,
		};
  		
	}


	onResize(){
		// 當視窗寬度小於768時 移除捲軸
		$(window).resize(function(){
      if($(this).width()>768){
        $('.diary-scroll-container').mCustomScrollbar({ axis: 'y', theme: 'minimal-dark' }); //apply scrollbar with your options 
      }else{
        $('.diary-scroll-container').mCustomScrollbar("destroy"); //destroy scrollbar 
      }
    }).trigger("resize");
	}

}
