import { Component, OnInit, Input } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import { CookieService } from '../../services/cookie.service';
import { LayoutService } from '../../services/layout.service';
import { NgxGalleryOptions, NgxGalleryImage, NgxGalleryAnimation } from 'ngx-gallery';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
	selector: 'app-product',
	templateUrl: './product.component.html',
	styleUrls: ['./product.component.css']
})
export class ProductComponent implements OnInit {

	product;
	sliders;
	fileUrl = Config.FileUrl;

	galleryOptions: NgxGalleryOptions[];
	galleryImages: NgxGalleryImage[] = [];

	iframeSrc;
	review_param;
	recommend_param;

	constructor(public sanitizer: DomSanitizer, protected httpService:HttpService, protected route:ActivatedRoute, protected cookieService:CookieService, private layoutService:LayoutService) 
	{ 
		//this.layoutService.setMeta();
	}

	ngOnInit() {
		this.route.params.subscribe(params => {
				this.getProduct(params['id']);
				this.getSliders(params['id']);
				this.review_param = { 'id': params['id'], 'from':0 };

			});

		this.galleryOptions = [
			{
				width: '100%',
				height: '100%',
				thumbnailsColumns: 4,
				thumbnailsPercent: 19,
				imageAnimation: NgxGalleryAnimation.Slide,
				previewSwipe: true,
				previewCloseOnClick: true,
				// previewZoom: true,
				arrowPrevIcon: 'arrow_carrot-left',
				arrowNextIcon: 'arrow_carrot-right',
				closeIcon: 'icon_close',
				// zoomInIcon: 'icon_zoom-in_alt',
				// zoomOutIcon: 'icon_zoom-out_alt'
			}
		];

	}


	//取得商品資訊
	getProduct(id){
		let url = `${Config.APIUrl}/api/shop/product/show?id=${id}&viewed=1`;
		this.httpService.getHttp(url).subscribe(
			res => {
				console.log(res.product)
				this.product = res.product;
				this.layoutService.setMetaData(res.product.metadata);

				if(this.product.detail.introduction_video){
					this.iframeSrc = this.sanitizer.bypassSecurityTrustResourceUrl(this.product.detail.introduction_video);
				}

				if(this.product.detail.agenda){
					this.product.detail.agenda = this.sanitizer.bypassSecurityTrustHtml(this.product.detail.agenda);
				}

				if(this.product.content)
					this.product.content = this.sanitizer.bypassSecurityTrustHtml(this.product.content);

				this.recommend_param = {
					'id': id,
					'type': 'product', 
					'from':0,
				};
			}
		);
	}


	//取得商品幻燈片
	getSliders(id){
		let url = `${Config.APIUrl}/api/shop/product/sliders?id=${id}`;
		this.httpService.getHttp(url).subscribe(
			res => {
				this.sliders = res.sliders;
				this.galleryImages = [];
				Object.keys(this.sliders).map(key => {
					let slider = { 
						small: `${Config.FileUrl}${this.sliders[key].filepath}`,
						medium: `${Config.FileUrl}${this.sliders[key].filepath}`,
						big: `${Config.FileUrl}${this.sliders[key].filepath}`,
					};
					this.galleryImages.push(slider);
				});
				console.log(this.galleryImages);
			}
		);
	}

}
