import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import * as Config from '../../models/config.model';
import { HttpService } from '../../services/http.service';


@Component({
	selector: 'app-product-recommend',
	templateUrl: './product-recommend.component.html',
	styleUrls: ['./product-recommend.component.scss']
})
export class ProductRecommendComponent implements OnInit {



	//input 顯示序號
	@Input() input_param;


	//如果input進來的參數改變, 重新取得心理測驗列表
	ngOnChanges(changes: SimpleChanges) {
		console.log(changes);
    	this.input_param = changes.input_param.currentValue;
    	this.readmoreCheck = 0;

    	this.getProductList();
	}

	
	list;
	fileUrl = Config.FileUrl;
	//是否開啟read more div
	readmoreCheck = 0;
	//一次要顯示幾筆
	limit = 4;
	moredata = 1;
	nodata = 0;

	constructor(private http:HttpService) { }

	ngOnInit() {
		this.readmoreCheck = 0;
		
    	this.getProductList();

	}




	//看更多
	more() {
		this.readmoreCheck = 1;
		this.input_param.from+=this.limit;	
	}


	getLink(type){
		let url;
		switch (type) {
			case "e-book":
				url = "/product/ebook/info";
				break;

			case "course":
				url = "/product/course/info";
				break;

			case "activity":
				url = "/product/activity/info";
				break;
			
			default:
				// code...
				break;
		}
		return url;
	}




	//取得所有文章測驗列表
	getProductList() {
		let url:string;

		
		const paramObject = {
			'id': this.input_param.id,
			'type': this.input_param.type,
			'res_type': 'product',
			'from': this.input_param.from,
			'limit':this.limit,
		};
		let param = this.http.getParamFormat(paramObject);
		url = `${Config.APIUrl}/api/recommend?${param}`;
		
		this.http.loadingOpen = false;
		this.http.getHttp(url).subscribe(
			res => {
				//沒有資料 隱藏more data區塊
				if(res.items.length==0){
					console.log("empty array");
					this.moredata = 0;
					this.nodata = 1;
				}

				this.list = res.items;
				console.log(res);
				this.http.loadingOpen = true;
			}
		);

	}

}
