import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import { Meta, Title } from '@angular/platform-browser';
//import * as $ from 'jquery';

@Component({
	selector: 'app-banner',
	templateUrl: './banner.component.html',
	styleUrls: ['./banner.component.scss']
})
export class BannerComponent implements OnInit {

	@Input() inputType;
	banners;
	fileUrl = Config.FileUrl;

	//
	mySlideImages = [1,2,3].map((i)=> `https://picsum.photos/640/480?image=${i}`);
	//myCarouselImages =[1,2,3,4,5,6].map((i)=>`https://picsum.photos/640/480?image=${i}`);
	mySlideOptions={items: 1, dots: true, loop: true, animateOut: 'fadeOut', autoplay:true, autoplayHoverPause:true};
	//myCarouselOptions={items: 3, dots: true, nav: true};

	constructor(private meta:Meta, private http:HttpService) { }

	ngOnInit() {
		this.getBannerList();
		this.uiControl();
	
	}




	//取得類別列表
	getBannerList() {
		let url:string;

		const paramObject = {
			'website_id': 1,
			'type': this.inputType,
		};
		//console.log(paramObject);

		let param = this.http.getParamFormat(paramObject);
		url = `${Config.APIUrl}/api/content/banner?${param}`;
		this.http.getHttp(url).subscribe(
			res => {
				this.banners = res.banners;
				console.log(res);
				this.mySlideImages = res.banners.map((banner)=> `${this.fileUrl}${banner.filepath}`);
				console.log(this.mySlideImages);

				//設定分享的內容縮圖

				this.meta.addTags([
			  		{ property: 'og:image', content: `${this.fileUrl}${res.banners[0].filepath}` },
			  	]);
			}
		);

	}

	// ui jquery
	uiControl(){
		$('.custom-arrow').click(function(event) {
		  event.preventDefault();
		  $('body,html').animate({
		    scrollTop: $('.section-home:first').offset().top - $('.header').height()
		  }, 600);
		})
	}

}
