import { Component, OnInit } from '@angular/core';
import { MemberService } from '../member.service';
import  * as Config from '../../models/config.model';

@Component({
	selector: 'app-member-menu',
	templateUrl: './member-menu.component.html',
	styleUrls: ['./member-menu.component.css']
})
export class MemberMenuComponent implements OnInit {

	fileUrl = Config.FileUrl;

	constructor(public memberService: MemberService) { }

	ngOnInit() {
		this.memberService.getUser();
		this.uiControl();
	}

	// ui jquery
	uiControl(){
		// 手機選單
		$('.nav-link').click(function(){
			$('.member-side').removeClass('open');
		})
	}

}
