import { Component, OnInit, Input } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para

@Component({
	selector: 'app-hashtag-home',
	templateUrl: './hashtag-home.component.html',
	styleUrls: ['./hashtag-home.component.scss']
})
export class HashtagHomeComponent implements OnInit {

	keyword;
	type;

	input_param_hashtag;
	dairy_param;
	pscholoy_param;
	ebook_param;
	course_param;
	activity_param;
	article_param;
	test_ans_param;

	constructor(private httpService:HttpService, protected route:ActivatedRoute) { }

	ngOnInit() {
		this.route.params.subscribe(params => {
    		this.keyword = params['id'];
    		this.input_param_hashtag = {
    			'keyword': this.keyword,
    			'from':0,
				'hashtag':1
    		};
    		if(params['type'])
    			this.selectType(params['type']);
    		else
    			this.selectType('diary');
    	});
	}



	selectType(type){

		//limit 設定各種hashtag搜尋結果的筆數
		this.dairy_param = Object.assign({}, this.input_param_hashtag); //copy object
		this.pscholoy_param = Object.assign({}, this.input_param_hashtag);
		this.ebook_param = Object.assign({}, this.input_param_hashtag);
		this.ebook_param.type = 'e-book';
		this.course_param = Object.assign({}, this.input_param_hashtag);
		this.course_param.type = 'course';
		this.activity_param = Object.assign({}, this.input_param_hashtag);
		this.activity_param.type = 'activity';
		this.article_param = Object.assign({}, this.input_param_hashtag);
		this.test_ans_param = Object.assign({}, this.input_param_hashtag);

		//顯示區塊
		console.warn(this.input_param_hashtag);
		this.type = type;
	}

}
