import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';


//calendar
import { CalendarModule, DateAdapter } from 'angular-calendar';
import { adapterFactory } from 'angular-calendar/date-adapters/date-fns';

//import module
import { FormsModule } from '@angular/forms';
import { ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule,  HTTP_INTERCEPTORS }    from '@angular/common/http';
import { NgHttpLoaderModule } from 'ng-http-loader';
//import { SweetAlertService } from 'angular-sweetalert-service';
import { SweetAlert2Module } from '@toverux/ngx-sweetalert2';

//angular material
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {MatButtonModule, MatCheckboxModule, MatNativeDateModule} from '@angular/material';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatFormFieldModule } from '@angular/material/form-field';
import {MatInputModule} from '@angular/material/input';
import {MatDialogModule} from '@angular/material/dialog';
import {MatAutocompleteModule} from '@angular/material/autocomplete';

//ckeditor
import { CKEditorModule } from 'ngx-ckeditor';

//banner
import { OwlModule } from 'ngx-owl-carousel';
import { SwiperModule } from 'ngx-swiper-wrapper';
import { SWIPER_CONFIG } from 'ngx-swiper-wrapper';
import { SwiperConfigInterface } from 'ngx-swiper-wrapper';

// custom scrollbar
import { MalihuScrollbarModule } from 'ngx-malihu-scrollbar';

// gallery
import { NgxGalleryModule } from 'ngx-gallery';

// pipes
import {NgPipesModule} from 'ngx-pipes';

//ngx-image-cropper
import { ImageCropperModule } from 'ngx-image-cropper';

//img cropper
import { CropperModule } from 'ngx-cropper';

// import { ChartsModule } from 'ng2-charts/ng2-charts';

//component
import { DatepickerComponent } from './datepicker/datepicker.component';
import { FileUploadComponent } from './file-upload/file-upload.component';
import { CkeditorComponent } from './ckeditor/ckeditor.component';
import { CategoryComponent } from '../layout/category/category.component';
import { BannerComponent } from '../layout/banner/banner.component';
import { ProductComponent } from './product/product.component';
import { CalendarComponent } from './calendar/calendar.component';
import { CartAddComponent } from './cart-add/cart-add.component';
import { SingleWriterComponent } from './single-writer/single-writer.component';
import { ShareRoutingModule } from './share-routing/share-routing.module';
import { ErrorComponent } from './error/error.component';
import { BuyLaterComponent } from './buy-later/buy-later.component';


@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    NgHttpLoaderModule,
    SweetAlert2Module.forRoot(),
    CalendarModule.forRoot({
	  	provide: DateAdapter,
	  	useFactory: adapterFactory
	}),
	//BrowserAnimationsModule,
	MatButtonModule, MatCheckboxModule, MatNativeDateModule,
	MatDatepickerModule,
	MatFormFieldModule,
	MatInputModule,
	MatDialogModule,
	MatAutocompleteModule,
	CKEditorModule,
	ShareRoutingModule,
	OwlModule,
	SwiperModule,
	MalihuScrollbarModule.forRoot(),
	NgPipesModule,
	NgxGalleryModule,
	ImageCropperModule,
	CropperModule,
	// ChartsModule
  ],
  declarations: [
  	DatepickerComponent,
	FileUploadComponent,
	CkeditorComponent,
	CategoryComponent,
	BannerComponent,
	ProductComponent, 
	CalendarComponent,
	CartAddComponent,
	SingleWriterComponent,
	ErrorComponent,
	BuyLaterComponent
  ],
  exports: [
  	FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    NgHttpLoaderModule,
    SweetAlert2Module,
    DatepickerComponent,
	FileUploadComponent,
	CkeditorComponent,
	CategoryComponent,
	BannerComponent,
	ProductComponent, 
	CalendarComponent,
    CalendarModule,
	//BrowserAnimationsModule,
	MatButtonModule, MatCheckboxModule, MatNativeDateModule,
	MatDatepickerModule,
	MatFormFieldModule,
	MatInputModule,
	MatDialogModule,
	MatAutocompleteModule,
	CKEditorModule,
	CartAddComponent,
	SingleWriterComponent,
	ErrorComponent,
	BuyLaterComponent,
	OwlModule,
	SwiperModule,
	MalihuScrollbarModule,
	NgPipesModule,
	NgxGalleryModule
  ]
})
export class ShareModule { }
