import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import { formatDate } from '@angular/common';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para

@Component({
	selector: 'app-mydiary',
	templateUrl: './mydiary.component.html',
	styleUrls: ['./mydiary.component.css']
})


export class MydiaryComponent implements OnInit {

	//input 顯示序號
	@Input() inputMydiary;

	diarylist;
	fileUrl = Config.FileUrl;
	diary_cover = Config.diaryCover;
	//是否開啟read more div
	readmoreCheck = 0;
	//一次要顯示幾筆
	limit = 6;
	moredata = 1;
	nodata = 0;

	constructor(private http:HttpService, private router:Router, private route:ActivatedRoute) { }

	//如果input進來的參數改變, 重新取得日記列表
	ngOnChanges(changes: SimpleChanges) {
		console.log(changes);
    	this.inputMydiary = changes.inputMydiary.currentValue;
    	this.readmoreCheck = 0;

    	//hashtag
    	if(this.inputMydiary.hashtag==1)
    		this.getDiaryHashtag();
    	//all
    	else
    		this.getDiaryList();
	}


	ngOnInit() {
		this.readmoreCheck = 0;
		/*第一次設定input 初始序號為0
		if(!this.inputMydiary)
			this.inputMydiary.from = 0;
		*/
		//hashtag
    	if(this.inputMydiary.hashtag==1)
    		this.getDiaryHashtag();
    	//all
    	else
    		this.getDiaryList();
	}


	//看更多
	more() {
		
		this.readmoreCheck = 1;
		this.inputMydiary.from+=this.limit;
		
	}


	//進入修改日記
	edit(id) {
		console.log(id);
		this.router.navigate(['/diary/edit', id]);
	}


	//取得hashtag列表
	getDiaryHashtag() {
		let url:string;
		const paramObject = {
			'keyword': this.inputMydiary.keyword,
			'from': this.inputMydiary.from,
			'limit':this.limit,
		};
		//console.log(paramObject);

		let param = this.http.getParamFormat(paramObject);
		url = `${Config.APIUrl}/api/member/diary/hashtag?${param}`;
		this.http.getHttp(url).subscribe(
			res => {
				console.log(res);
				
				//沒有資料 隱藏more data區塊
				if(res.diaries.length==0){
					console.log("empty array");
					this.moredata = 0;
					this.nodata = 1;
				}

				this.diarylist = res.diaries;
				
			}
		);
	}


	//取得日記列表
	getDiaryList() {
		let url:string;
		const enddate = new Date();
		let startdate = new Date();
		startdate.setMonth(startdate.getMonth() - 1);;
		const paramObject = {
			'start_date': this.inputMydiary.startdate,
			'end_date': this.inputMydiary.enddate,
			'from': this.inputMydiary.from,
			'limit':this.limit,
		};
		//console.log(paramObject);

		let param = this.http.getParamFormat(paramObject);
		url = `${Config.APIUrl}/api/member/diary?${param}`;
		this.http.getHttp(url).subscribe(
			res => {
				//沒有資料 隱藏more data區塊
				if(res.diaries.length==0){
					console.log("empty array");
					this.moredata = 0;
					this.nodata = 1;
				}

				this.diarylist = res.diaries;
				console.log(res);
			}
		);

	}

}
