import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import { formatDate } from '@angular/common';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para

@Component({
	selector: 'app-psychology-list',
	templateUrl: './psychology-list.component.html',
	styleUrls: ['./psychology-list.component.scss']
})
export class PsychologyListComponent implements OnInit {

	//input 顯示序號
	@Input() psychology_param;


	//如果input進來的參數改變, 重新取得心理測驗列表
	ngOnChanges(changes: SimpleChanges) {
		console.log(changes);
    	this.psychology_param = changes.psychology_param.currentValue;
    	this.readmoreCheck = 0;
    	
    	if(this.psychology_param.hashtag==1)
			this.getPsychologyHashtag();
		else
			this.getPsychologyList();
	}

	
	list;
	fileUrl = Config.FileUrl;
	//是否開啟read more div
	readmoreCheck = 0;
	//一次要顯示幾筆
	limit = 9;
	moredata = 1;
	nodata = 0;

	constructor(private http:HttpService, private router:Router, private route:ActivatedRoute) { }

	ngOnInit() {
		this.readmoreCheck = 0;

		if(this.psychology_param.hashtag==1)
			this.getPsychologyHashtag();
		else
			this.getPsychologyList();
	}


	//看更多
	more() {
		this.readmoreCheck = 1;
		this.psychology_param.from+=this.limit;	
	}


	//取得心理測驗hashtag
	getPsychologyHashtag() {
		let url:string;

		const paramObject = {
			'keyword': this.psychology_param.keyword,
			'from': this.psychology_param.from,
			'limit':this.limit,
		};
		//console.log(paramObject);

		let param = this.http.getParamFormat(paramObject);
		url = `${Config.APIUrl}/api/content/psychological-test/hashtag?${param}`;
		this.http.getHttp(url).subscribe(
			res => {
				//沒有資料 隱藏more data區塊
				if(res.tests.length==0){
					console.log("empty array");
					this.moredata = 0;
					this.nodata = 1;
				}

				this.list = res.tests;
				console.log(res);
			}
		);

	}


	//取得心理測驗列表
	getPsychologyList() {
		let url:string;

		const paramObject = {
			'website_id': 1,
			'category_id': this.psychology_param.category_id,
			'sort': this.psychology_param.sort,
			'sort_order': this.psychology_param.sort_order,
			'from': this.psychology_param.from,
			'limit':this.limit,
		};
		//console.log(paramObject);

		let param = this.http.getParamFormat(paramObject);
		url = `${Config.APIUrl}/api/content/psychological-test?${param}`;
		this.http.getHttp(url).subscribe(
			res => {
				//沒有資料 隱藏more data區塊
				if(res.tests.length==0){
					console.log("empty array");
					this.moredata = 0;
					this.nodata = 1;
				}

				this.list = res.tests;
				console.log(res);
			}
		);

	}

}
