import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { HttpService } from '../../services/http.service';
import * as Config from '../../models/config.model';
import swal, { SweetAlertOptions } from 'sweetalert2';

@Component({
	selector: 'app-diary-background-list',
	templateUrl: './diary-background-list.component.html',
	styleUrls: ['./diary-background-list.component.scss']
})
export class DiaryBackgroundListComponent implements OnInit {

	@Input() input_param;
	list;
	fileUrl = Config.FileUrl;
	//是否開啟read more div
	readmoreCheck = 0;
	//一次要顯示幾筆
	limit = 8;
	moredata = 1;
	nodata = 0;

	//如果input進來的參數改變, 重新取得心理測驗列表
	ngOnChanges(changes: SimpleChanges) {
		console.log(changes);
    	this.input_param = changes.input_param.currentValue;
    	this.readmoreCheck = 0;

    	this.getList();
	}

	
	

	constructor(private httpService:HttpService) { }

	ngOnInit() {
		this.readmoreCheck = 0;
		this.getList();
	}


	//設定背景
	setBackground(id, title){
		let params:FormData = new FormData();
  		params.append('title', title);
  		params.append('id', id);
  		params.append('default_image', '1');
  		params.append('_method', 'PUT');

		let url = `${Config.APIUrl}/api/member/diary-background`;

		this.httpService.postHttp(url, params).subscribe(
			res => location.reload()
		);
	}


	//刪除圖片
	deleteBg(id){

		swal({
	    		title: `刪除背景`,
			  	text: "確定要刪除此背景?",
			  	type: "question",
			  	showCancelButton:true,
			  	focusCancel:true,
					
			}).then(
				(success) => {
					console.log(success);
					if(success.value){
						let url = `${Config.APIUrl}/api/member/diary-background?id=${id}`;
						this.httpService.deleteHttp(url).subscribe(
							res => location.reload()
						);
						
					}
	    	});

		
	}



	//取得日記背景清單
	getList(){
		const paramObject = {
				'from': this.input_param.from,
				'limit':this.limit,
			};
		let param = this.httpService.getParamFormat(paramObject);
		let url = `${Config.APIUrl}/api/member/diary-background?${param}`;
		this.httpService.getHttp(url).subscribe(
				res => {
						//沒有資料 隱藏more data區塊
						if(res.backgrounds.length==0){
							console.log("empty array");
							this.moredata = 0;
							this.nodata = 1;
						}

						this.list = res.backgrounds;
						console.log(res);
					}
		);

		
	}


	//看更多
	more() {
		this.readmoreCheck = 1;
		this.input_param.from+=this.limit;	
	}



}
