import { Component, OnInit } from '@angular/core';
import { LayoutService } from "../../services/layout.service";
import { HttpService } from '../../services/http.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import * as Config from '../../models/config.model';

@Component({
  selector: 'app-diary-description',
  templateUrl: './diary-description.component.html',
  styleUrls: ['./diary-description.component.scss']
})
export class DiaryDescriptionComponent implements OnInit {

	subscription;

	// scrollbar套件設定
	public scrollbarOptions = { axis: 'y', theme: 'minimal-dark' };

  constructor(private router:Router, private httpService:HttpService, public layoutService:LayoutService) { }

  ngOnInit() {
		this.layoutService.getParam("diary_description");
		this.getSubscription();
	}


	getSubscription(){
		let url = `${Config.APIUrl}/api/member/subscription/status`;
		this.httpService.getHttp(url).subscribe(
			res => this.subscription = res
		);
	}


	trial(){
		let param = null;
		let url = `${Config.APIUrl}/api/member/subscription/free`;

		this.httpService.postHttp(url, param).subscribe(
			res => this.router.navigate(['/diary'])
		);

	}

}
