import { Injectable } from '@angular/core';
import { HttpService } from '../services/http.service';
import  * as Config from '../models/config.model';

//日歷點點顏色
const colors: any = {
	red: {
			primary: '#c9caca', //gray
			secondary: '#dcddde' //light
	},
	blue: {
			primary: '#a0aa58', //diary
			secondary: '#d5dabc' //diary-light
	},
	yellow: {
			primary: '#e3bc08',
			secondary: '#FDF1BA'
	}
};

@Injectable({
	providedIn: 'root'
})
export class DiaryService {

	calendarEvents;
	default_cover;
	constructor(private httpService:HttpService) { }


	//取得特定網站參數
	getCover(){
		let url = `${Config.APIUrl}/api/content/parameter?name=default_diary_cover`;
		this.httpService.getHttp(url).subscribe(
			res => {
				this.default_cover = res.value;
				console.log(res);
			}
		);
	}


	//取得已寫過的日記日期
	getDiaryDates() {
		const url = `${Config.APIUrl}/api/member/diary/calendar`;
		this.httpService.getHttp(url)
			.subscribe(
				res => {
					//console.log(res.datas);
					this.calendarEvents = [];
					Object.keys(res.datas).map(
						key=> {
							// temp is resultsType[]
							//console.log(res.datas[key].diary_date);
							let dot_color = colors.red;
							if(res.datas[key].status=="1")
								dot_color = colors.blue;
							let temp = {start: new Date(res.datas[key].diary_date), title: res.datas[key].title, id: res.datas[key].id, color: dot_color};
							this.calendarEvents.push(temp);
							//console.log(this.calendarEvents);
						}
					);
				}
			);
		
	}

}
