import { Component, OnInit } from '@angular/core';
import { LayoutService } from "../../services/layout.service";
import { HttpService } from '../../services/http.service';
import * as Config from '../../models/config.model';
import { formatDate } from '@angular/common';
import { SwiperComponent, SwiperDirective, SwiperConfigInterface,
  SwiperScrollbarInterface, SwiperPaginationInterface } from 'ngx-swiper-wrapper';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
  
@Component({
	selector: 'app-article-home',
	templateUrl: './article-home.component.html',
	styleUrls: ['./article-home.component.css']
})
export class ArticleHomeComponent implements OnInit {

	input_param; 
	category_id = 0;
	year = 0;
	type = 'post_date';
	order = 'desc';
	article_slides;
	fileUrl = Config.FileUrl;
	years= [];
	slide_index = 0;
	input_category_ids = {
		'parent_id' : 0,
		'category_id' : 0,
	};

	public configCover: SwiperConfigInterface = {
    slidesPerView: 1,
    scrollbar: false,
    effect: 'fade',
    speed: 1000,
    autoplay: {
      delay: 2500,
      disableOnInteraction: false,
    },
    navigation: false,
    pagination: {
      el: '.swiper-pagination',
      clickable: true,
    },
	};

	public configMenu: SwiperConfigInterface = {
		a11y: true,
    direction: 'vertical',
    slidesPerView: 5,
    slidesPerGroup: 5,
    scrollbar: false,
    navigation: true,
    pagination: {
      el: '.swiper-pagination',
      clickable: true,
    },
	};

	public onIndexChange(index: number): void {
		this.slide_index = index;
	  // console.log('Swiper index: ', index);
	}

	constructor(public layoutService:LayoutService, private httpService:HttpService, private route:ActivatedRoute) { }

	ngOnInit() {
		this.layoutService.getSummary('page_article_introduction');
		this.layoutService.setMetaId(48);
		this.route.params.subscribe(params=>{
			if(params['id']){
				this.category_id = params['id'];
				this.input_category_ids = {
					'parent_id' : params['parentId'],
					'category_id' : params['id'],
				};
			}
			this.input_param = {
				website_id: 1,
				category_id: this.category_id,
				sort: this.type,
				sort_order: this.order,
				year: this.year,
				from: 0,
				favorite: 0,
			};
		});
		
		
		//時間篩選
		let thisYear = formatDate(new Date(), 'yyyy', 'en-US');
		for(let i=2010; i<=Number(thisYear); i++){
			this.years.push(i);
		}

		//幻燈片
		this.articleSlide();

		//幻燈片切換
		// this.slideControl();
		this.uiControl();
	}


	//sort 一般篩選器
	sortType($type,$order){
		this.type = $type;
		this.order = $order;
		this.input_param = {
			website_id: 1,
			category_id: this.category_id,
			sort: $type,
			sort_order: $order,
			year: this.year,
			from: 0,
			favorite: 0,
		};
	}


	//sort 時間篩選器
	sortYear($year){
		this.year = $year;
		this.input_param = {
			website_id: 1,
			category_id: this.category_id,
			sort: this.type,
			sort_order: this.order,
			year: $year,
			from: 0,
			favorite: 0,
		};
	}


	//category component output
	getCategoryId($id){
		this.category_id = $id;
		this.input_param = {
			website_id: 1,
			category_id: $id,
			sort: 'post_date',
			sort_order: 'desc',
			year: this.year,
			from: 0,
			favorite: 0,
		};
		//location.reload();
	}


	//主題撥放
	articleSlide(){
		let url = `${Config.APIUrl}/api/content/article/hot?website_id=1`;
		this.httpService.getHttp(url).subscribe(
			res => this.article_slides = res.articles 
		);
	}


	//主題slider
	// slideControl(){
	// 	$("body").on("mouseover", ".slider-menu-item", function(){
	// 	  let index = $(this).index();
	// 	  $('.slider-desktop .slider-item').removeClass('active').eq(index).addClass('active');

	// 	  $('.slider-menu-item').removeClass('active');
	// 	  $(this).addClass('active');
	// 	});
	// }

	// ui jquery
	uiControl(){
		// 類別篩選器
		$('.toggle-category').click(function(){
			$(this).children('[data-icon]').toggle();
			$('.dropdown-category').toggle();
		});
		// 篩選年份
    $('body').on('click', '.category-year', function(){
    	$('.category-year').removeClass('active');
    	$(this).addClass('active');
    })
	}
}

