import { Injectable } from '@angular/core';
import { HttpService } from './http.service';
import  * as Config from '../models/config.model';
import { Meta, Title } from '@angular/platform-browser';
import { DomSanitizer } from '@angular/platform-browser';

@Injectable({
	providedIn: 'root'
})
export class LayoutService {

	param;
	page_summary;
	module:any = {};
	constructor(private sanitizer: DomSanitizer, private httpService:HttpService, private meta: Meta, private title: Title) { }


	//設定metadata
	setMetaId(id){
		//console.log("111111111111111111111");
		let url = `${Config.APIUrl}/api/content/metadata?id=${id}`;
		this.httpService.getHttp(url).subscribe(
			res => {

				this.meta.updateTag({ name: 'keyword', content: res.metadata.keyword },`name='keyword'`);
				this.meta.updateTag({ name: 'title', content: res.metadata.title },`name='title'`);
				this.meta.updateTag({ name: 'description', content: res.metadata.description },`name='description'`);

				this.title.setTitle(res.metadata.title);
				/*
				this.meta.addTags([
  					{ name: 'keyword', content: res.metadata.keyword },
  					{ name: 'title', content: res.metadata.title },
  					{ name: 'description', content: res.metadata.description },
  				]);
  				*/
			}
		);
	}


	//設定metadata
	setMetaData(metadata){
		console.log(metadata);
		this.meta.updateTag({ name: 'keyword', content: metadata.keyword },`name='keyword'`);
		this.meta.updateTag({ name: 'title', content: metadata.title },`name='title'`);
		this.meta.updateTag({ name: 'description', content: metadata.description },`name='description'`);

		this.title.setTitle(metadata.title);

		/*
		this.meta.addTags([
  			{ name: 'keyword', content: metadata.keyword },
  			{ name: 'title', content: metadata.title },
  			{ name: 'description', content: metadata.description },
  		]);
  		*/
		
	}


	//取得單一區塊內容
	getSummary(code) {
		let url = `${Config.APIUrl}/api/content/module?website_id=1&code=${code}`;
		this.httpService.getHttp(url).subscribe(
			res => this.page_summary = res.module.content
		);
	}


	//首頁取得整個模組內的所有區塊內容
	getModule(code){
		let url = `${Config.APIUrl}/api/content/block?website_id=1&code=${code}`;
		this.httpService.getHttp(url).subscribe(
			res => {
				this.module[code] = res;
				console.log(this.module);
			}
		);
	}


	//取得特定網站參數
	getParam(name){
		let url = `${Config.APIUrl}/api/content/parameter?name=${name}`;
		this.httpService.getHttp(url).subscribe(
			res => {
				this.param = this.sanitizer.bypassSecurityTrustHtml(res.value);
				console.log(res);
			}
		);
	}

}
