import { Component, OnInit, Input, SimpleChanges  } from '@angular/core';
import { HttpService } from '../../services/http.service';
import * as Config from '../../models/config.model';

@Component({
	selector: 'app-article-recommend',
	templateUrl: './article-recommend.component.html',
	styleUrls: ['./article-recommend.component.scss']
})
export class ArticleRecommendComponent implements OnInit {

	@Input() input_param;
	list;
	limit = 4;
	fileUrl = Config.FileUrl;

	constructor(private httpService:HttpService) { }

	ngOnInit() {
		this.getArticle();
	}

	//如果input進來的參數改變, 重新取得心理測驗列表
	ngOnChanges(changes: SimpleChanges) {
		console.log(changes);
    	this.input_param = changes.input_param.currentValue;
    	this.getArticle();
	}


	getArticle(){
		let url:string;

		
		const paramObject = {
			'id': this.input_param.id,
			'type': this.input_param.type,
			'res_type': 'article',
			'from': this.input_param.from,
			'limit':this.limit,
		};
		let param = this.httpService.getParamFormat(paramObject);
		url = `${Config.APIUrl}/api/recommend?${param}`;
		
		this.httpService.loadingOpen = false;
		this.httpService.getHttp(url).subscribe(
			res => {

				this.list = res.items;
				console.log(res);
				this.httpService.loadingOpen = true;
			}
		);
	}

}
