import { Component, OnInit } from '@angular/core';
import  * as Config from '../../models/config.model';
import { CookieService } from '../../services/cookie.service';
import { MemberService } from '../../member/member.service';
import { LayoutService } from "../../services/layout.service";
import { HttpService } from '../../services/http.service';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';

@Component({
	selector: 'app-diary-cover',
	templateUrl: './diary-cover.component.html',
	styleUrls: ['./diary-cover.component.css']
})
export class DiaryCoverComponent implements OnInit {

	avatar;
	config = Config;
	subscription;

	constructor(private router:Router, private httpService:HttpService, public layoutService:LayoutService, private cookieService: CookieService, public memberService:MemberService) { }

	ngOnInit() {
		this.layoutService.setMetaId(51);
		this.getSubscription();
	}


	getSubscription(){
		let url = `${Config.APIUrl}/api/member/subscription/status`;
		this.httpService.getHttp(url).subscribe(
			res => this.subscription = res
		);
	}


	trial(){
		let param = null;
		let url = `${Config.APIUrl}/api/member/subscription/free`;

		this.httpService.postHttp(url, param).subscribe(
			res => this.router.navigate(['/diary'])
		);

	}

}
