import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import  * as Config from '../../models/config.model';
import { HttpService } from '../../services/http.service';
import { LayoutService } from '../../services/layout.service';
import { SocialShareService } from '../../services/social-share.service';
import { Meta, Title } from '@angular/platform-browser';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
	selector: 'app-article-read',
	templateUrl: './article-read.component.html',
	styleUrls: ['./article-read.component.css']
})
export class ArticleReadComponent implements OnInit {

	article;
	article_id;
	fileUrl = Config.FileUrl;
	recommend_param;
	content;

	constructor(private sanitizer: DomSanitizer, private meta:Meta, private shareService:SocialShareService, private route: ActivatedRoute, private http:HttpService, private layoutService:LayoutService) { }

	ngOnInit() {
		this.route.params.subscribe(params => {
    		this.getArticle(params['id']);
    		this.article_id = params['id'];
    		this.recommend_param = {
    			'id': this.article_id,
    			'from': 0,
    			'type': 'article',
    		};

    	});
	}

	


	//社群分享
	socialShare(type) {
		let url;
		switch (type) {
			case "line":
				url = `${Config.angularUrl}/writer/article/read/${this.article_id}`;
				this.shareService.lineShare(url);
				break;

			case "FB":
				url = `${Config.angularUrl}/writer/article/read/${this.article_id}`;
				this.shareService.FBShare(url);
				break;
			
			default:
				// code...
				break;
		}
	}


	//output for love. unfavorite => remove 
	onListenChild(event: any, aid){
		$(`#${aid}`).remove();
	}


	//取得單一文章
	getArticle(id){
		let url:string;

		url = `${Config.APIUrl}/api/content/article/show?id=${id}`;
		this.http.getHttp(url).subscribe(
			res => {
				//metadata
				this.layoutService.setMetaData(res.article.metadata);
				//設定分享的內容縮圖
				this.meta.addTags([
			  		{ property: 'og:title', content: res.article.title },
			  		{ property: 'og:description', content: res.article.summary },
			  		{ property: 'og:image', content: `${this.fileUrl}${res.article.image}` },
			  	]);

				this.article = res.article;
				this.content = this.sanitizer.bypassSecurityTrustHtml(this.article.content);
				console.log(res);
			}
		);
	}

}
