import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import  * as Config from '../../models/config.model';
import { HttpService } from '../../services/http.service';
import { NgForm, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { DomSanitizer } from '@angular/platform-browser';
import { DiaryService } from '../diary.service';

@Component({
	selector: 'app-diary-read',
	templateUrl: './diary-read.component.html',
	styleUrls: ['./diary-read.component.css']
})
export class DiaryReadComponent implements OnInit {

	diary;
	fileUrl = Config.FileUrl;
	diary_cover = Config.diaryCover;
	content;

	// scrollbar套件設定
	public scrollbarOptions = { axis: 'y', theme: 'minimal-dark' };

	constructor(public diaryService:DiaryService, private sanitizer: DomSanitizer, private router:Router, private route:ActivatedRoute, private http:HttpService) { }

	ngOnInit() {
		this.diaryService.getCover();
		this.getDiary();
		this.onResize();
	}


	


	//取得單一日記資訊
	getDiary(){
		const id = +this.route.snapshot.paramMap.get('id');
		const url = `${Config.APIUrl}/api/member/diary/show?id=${id}`;
		this.http.getHttp(url).subscribe(
				res => {
					this.diary = res;
					this.content = this.sanitizer.bypassSecurityTrustHtml(res.content);
					console.log(this.diary);
				}
			);
	}


	onResize(){
		// 當視窗寬度小於768時 移除捲軸
		$(window).resize(function(){
      if($(this).width()>768){
        $('.diary-scroll-container').mCustomScrollbar({ axis: 'y', theme: 'minimal-dark' }); //apply scrollbar with your options 
      }else{
        $('.diary-scroll-container').mCustomScrollbar("destroy"); //destroy scrollbar 
      }
    }).trigger("resize");
	}

}
