import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import  * as Config from '../../models/config.model';
import { HttpService } from '../../services/http.service';
import { NgForm, FormGroup, FormControl, Validators, FormBuilder, FormArray  } from '@angular/forms';
import swal, { SweetAlertOptions } from 'sweetalert2';
import { CustomService } from '../../validator/custom.service';
import { LayoutService } from '../../services/layout.service';
import { SocialShareService } from '../../services/social-share.service';
import { Meta, Title } from '@angular/platform-browser';

@Component({
	selector: 'app-questionnaire',
	templateUrl: './questionnaire.component.html',
	styleUrls: ['./questionnaire.component.css']
})

export class QuestionnaireComponent implements OnInit {

	data;
	fileUrl = Config.FileUrl;
	category;
	psychological_test_id = "3";

	submitted = false;
	Form = this.formBuilder.group({ });

	constructor(private meta:Meta, private shareService:SocialShareService, private layoutService:LayoutService, private route: ActivatedRoute, private router: Router, private httpService:HttpService, private formBuilder: FormBuilder, private customValidators: CustomService) { }

	ngOnInit() {
		this.route.params.subscribe(params => {
			this.psychological_test_id = params['id'];
			this.getInboundAPI();
    	});
		this.uiControl();


	}


	//社群分享
	socialShare(type) {
		let url;
		switch (type) {
			case "line":
				url = `${Config.angularUrl}/psychology/questionnaire/${this.psychological_test_id}`;
				this.shareService.lineShare(url);
				break;
			
			case "FB":
				url = `${Config.angularUrl}/psychology/questionnaire/${this.psychological_test_id}`;
				this.shareService.FBShare(url);
				break;

			default:
				// code...
				break;
		}
	}


	//使用英邦德單一問卷資料API
	getInboundAPI() {
		const paramObject = {
			'id': this.psychological_test_id,
			'viewed': 0,
		};
		//console.log(paramObject);
		let param = this.httpService.getParamFormat(paramObject);
		let url = `${Config.APIUrl}/api/content/psychological-test/show?${param}&viewed=1`;

		this.httpService.getHttp(url)
			.subscribe(
				res=> {
					//metadata
					this.layoutService.setMetaData(res.test.metadata);
					this.data = res.test;
					//設定分享的內容縮圖
					this.meta.addTags([
			  			{ property: 'og:title', content: this.data.title },
			  			{ property: 'og:description', content: this.data.summary },
			  			{ property: 'og:image', content: `${this.fileUrl}${this.data.image}` },
			  		]);

					
					//console.log(res);
					let questions = this.data.questionnaire.structure.sec_Questions.questions;
					Object.keys(questions)
						.map(key=> {
							//console.log(questions[key]);
							
							//單選 
							if(questions[key].type=="choice"){
								this.Form.addControl(questions[key].id, new FormControl('', Validators.required));
							}

							//複選
							if(questions[key].type=="multi"){
								this.Form.addControl(questions[key].id, new FormArray([], this.customValidators.checkboxRequired));

								Object.keys(questions[key].choices)
									.map(choice_key => {
										let control = <FormArray>this.Form.controls[questions[key].id];
										control.push(new FormControl(false));
											
									});
							}//複選end
						});
				} 
			);
	}


	//設定checkbox的值 從原本的true 改成value
	onCheckChange(event) {
		let eventId = event.target.id;
		let targetIds = eventId.split("#");
	  	const formArray: FormArray = this.Form.get(targetIds[0]) as FormArray;

		/* Selected */
		if(event.target.checked){
		   // Add a new control in the arrayForm
			//console.log(formArray.controls[targetIds[1]]);
		   	//console.log(targetIds[1]);
		   	formArray.controls[targetIds[1]].setValue(event.target.value);
		}
	}


	//表單送出
	onSubmit(){
		console.log(this.Form.value);
		
		//判斷是否已按了送出 按了就要顯示驗證訊息
		this.submitted = true;

		//表單沒有驗證成功 沒有動作
		if(this.Form.invalid){

			//focus for invalid Form
			Object.keys(this.Form.controls).map( key => {
				
				if(this.Form.controls[key].status == "INVALID"){
					let invalidElem: any = this.Form.controls[key];
					invalidElem.nativeElement.focus();
					return false;
				}
			});

			return;
		}

		let params:FormData = new FormData();
  		params.append('psychological_test_id', this.psychological_test_id);
  		let jsonStr = JSON.stringify(this.Form.value);
  		params.append('questions', jsonStr);

		console.log(jsonStr);
  		console.log(params.getAll('questions'));

  		const url = `${Config.APIUrl}/api/member/psychological-test-answer`;

	  	this.httpService.postHttp(url,params)
		    .subscribe(res => {
		    	console.log(res);
			    swal({
					title: '成功!',
					text: '心理測驗填寫完成',
					type: 'success',
				}).then(
					(success)=> {
			    	this.router.navigate(['../../result', this.psychological_test_id, res.answer_id], { relativeTo: this.route });
				});
				
		    });
	}


	uiControl(){
		// 點選答案後題目底色改變
		$('body').on('click', '.custom-control-input:checked', function(){
			$(this).closest('.form-group').siblings('.question-topic').addClass('active');
			$(this).closest('.form-group').siblings('.question-count').addClass('active');
		})
	}
	

}
