import { CartComponent } from "../member/cart/cart.component";
import { OrderEditComponent } from "../member/order-edit/order-edit.component";
/*
import { EbookInfoComponent } from "../ebook/ebook-info/ebook-info.component";
import { ActivityInfoComponent } from "../activity/activity-info/activity-info.component";
import { CourseInfoComponent } from "../course/course-info/course-info.component";
*/
//import { OrderRecordComponent } from "../member/order-record/order-record.component";
//import { OrderDetailComponent } from "../member/order-detail/order-detail.component";
import { ContentComponent } from "../layout/content/content.component";
import { ErrorComponent } from "../share/error/error.component";
import { GoogleSearchComponent } from "../layout/google-search/google-search.component";
import { PageComponent } from "../page/page.component";

//route
const routes: Routes = [

	//首頁模板
	{ path: '', component: HomeComponent,

		children: [
			//首頁預設內容
			{ path: '', component: ContentComponent },

			//hashtag 
			{ path: 'hashtag', loadChildren: '../hashtag/hashtag.module#HashtagModule' },

			//google search
			{ path: 'search/:keyword', component: GoogleSearchComponent },

			//日記本
			{ path: 'diary_cover', component: DiaryCoverComponent, canActivate:[CompleteInfoGuard] },
			{ path: 'diary_desc', component: DiaryDescriptionComponent, canActivate:[CompleteInfoGuard] },

			{ path: 'diary', loadChildren: '../diary/diary.module#DiaryModule' },
		
			//心理測驗
			{ path: 'psychology', loadChildren: '../psychology/psychology.module#PsychologyModule' },
			
			//商城
			{ path: 'product', loadChildren: '../product/product.module#ProductModule' },

			//作者專區
			{ path: 'writer', loadChildren: '../writer/writer.module#WriterModule' },

			//會員專區
			{ path: 'member', loadChildren: '../member/member.module#MemberModule' },
			
			//購物車
			{ path: 'cart', component: CartComponent, canActivate:[CompleteInfoGuard] },
			{ path: 'order_edit', component: OrderEditComponent, canActivate:[CompleteInfoGuard] },
			{ path: 'subscription_edit', component: OrderEditComponent, canActivate:[CompleteInfoGuard] },
		]
	},

	//{ path: 'search/:keyword', component: GoogleSearchComponent },

	//{ path: 'calendar', component: CalendarComponent },
	{ path: 'login', component: LoginComponent },
	{ path: 'login/:returnUrl', component: LoginComponent },
	{ path: 'register', component: RegisterComponent },
	//{ path: 'upload', component: FileUploadComponent },
	{ path: 'error404', component: ErrorComponent },
	//{ path: 'agreement', component: AgreementComponent },
	{ path: 'page/:code', component: PageComponent },
	
	//{ path: 'editor', component: CkeditorComponent },
	//{ path: 'useredit', component: UsereditComponent },
	//{ path: 'qqq', component: QuestionnaireComponent },
	
	//redirect
	{ path: 'home', redirectTo: '/', pathMatch: 'full' },
	{ path: 'agreement', redirectTo: '/page/agreement', pathMatch: 'full' },

	//其他未定義的route 導到首頁
	{ path: '**', redirectTo: '/'},

];


@NgModule({
	imports: [ RouterModule.forRoot(routes) ],
	exports: [ RouterModule ]
})


export class AppRoutingModule { }
