import { Component, OnInit } from '@angular/core';
import * as $ from 'jquery';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import { CookieService } from '../../services/cookie.service';
import { MemberService } from "../../member/member.service";
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material'; //popup dialog
import { LoginComponent } from '../../auth/login/login.component';
import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import { NgForm, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
// SSR
import { isPlatformBrowser } from '@angular/common';
import { Injectable, Inject, PLATFORM_ID } from '@angular/core';

@Component({
	selector: 'app-header',
	templateUrl: './header.component.html',
	styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {

	user;
	config = Config;
	keyword;
	search_type = "google";

	constructor(@Inject(PLATFORM_ID) private platformId: Object, private router:Router, private route: ActivatedRoute, public cookieService:CookieService, private httpService:HttpService, public memberService:MemberService, private dialog:MatDialog) { }

	ngOnInit() {
		this.user = this.cookieService.getCookie('name');
		if(this.user)
			this.memberService.getUser();

		this.uiControl();
	}


	login() {
		console.log(this.router.url);
		this.openDialog(this.router.url);
	}


	//search
	search(){
		console.log(this.keyword);

		//google
		if(this.search_type=="google"){
			this.router.navigate(['/search', this.keyword]);

		//hashtag
		}else if(this.search_type=="hashtag"){
			this.router.navigate(['/hashtag', this.keyword]);
		}
	}

	//開啟pop up
  openDialog(data){
  	const dialogRef = this.dialog.open(LoginComponent, {
  		maxWidth: '100%',
	  	width: '500px',
	  	data: { returnUrl: data}
		});
  }

  // ui jquery
	uiControl(){
		// BROWSER
 		if (isPlatformBrowser(this.platformId)) {
 			// 手機選單
 			$('.navbar-toggler, .navbar-overlay').click(function(){
 				$('.navbar-overlay').toggleClass('show');
 				$('.navbar-lnb .navbar-collapse').toggleClass('open');
 			})
 			$('.navbar-lnb a').click(function(){
 				$('.navbar-overlay').removeClass('show');
 				$('.navbar-lnb .navbar-collapse').removeClass('open');
 			})

 			// 搜尋
			$('.toggler-search').click(function(){
				$('.dropdown-search').toggle();
			});
			$('.btn-search').click(function(){
				if($('#search_input').val() != ''){
					$('.dropdown-search').toggle();
				}
			});
			// 點擊頁面其他位置隱藏搜尋bar
			$(document).click(function(event) {
				if(!$(event.target).is('.toggler-search *, .dropdown-search, .dropdown-search *')){
					$('.dropdown-search').hide();
				}
			});

			// 類別選單
	    $('body').on('click', '.category-year', function(){
	    	$('.category-year').removeClass('active');
	    	$(this).addClass('active');
	    });
		}
	}
}
