import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';

@Component({
	selector: 'app-diary-hashtag',
	templateUrl: './diary-hashtag.component.html',
	styleUrls: ['./diary-hashtag.component.scss']
})
export class DiaryHashtagComponent implements OnInit {

	@Input() input_diary_id;
	@Input() input_hashtags;
	name;
	edit;

	constructor(private httpService:HttpService) { }

	ngOnInit() {
		
	}


	//如果input進來的參數改變, 重新取得心理測驗列表
	ngOnChanges(changes: SimpleChanges) {
		console.log(changes);
    	this.input_diary_id = changes.input_diary_id.currentValue;
    	this.input_hashtags = changes.input_hashtags.currentValue;
	}


	//檢查hashtag是否重複
	check(name){
		let check_id = this.input_hashtags.findIndex(hashtag => hashtag.name === name); 
		console.log(check_id);
		return check_id;
	}



	//新增hashtag
	add(){
		let check_id = this.check(this.name);

		if(this.name && check_id==-1){
			let url = `${Config.APIUrl}/api/content/hashtag/diary`;
			let params:FormData = new FormData();
			params.append('id', this.input_diary_id);
			params.append('name', this.name);
			
			this.httpService.postHttp(url, params).subscribe(
				res => this.input_hashtags.push({
							'id': res.id,
							'name': this.name
						})
			);
		}
		console.log(this.input_hashtags);
	}


	//移除hashtag
	remove(hashtag_id){
		let url = `${Config.APIUrl}/api/content/hashtag/diary?id=${this.input_diary_id}&hash_tag_id=${hashtag_id}`;

		this.httpService.deleteHttp(url).subscribe(
			res => {
				const index = this.input_hashtags.findIndex(hashtag => hashtag.id === hashtag_id); 
				this.input_hashtags.splice(index, 1);
			}
		);

	}

}
