import { Component, OnInit } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';
import { CountryList } from '../../models/countrylist.model';

import {  Router, ActivatedRoute, ParamMap } from '@angular/router'; //接收route para
import swal, { SweetAlertOptions } from 'sweetalert2';
import { MemberService } from '../member.service'
import { NgForm, FormGroup, FormControl, Validators, FormBuilder } from '@angular/forms';
import { ProductService } from '../../services/product.service';
import { ThirdPartyComponent } from '../third-party/third-party.component';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

@Component({
	selector: 'app-order-edit',
	templateUrl: './order-edit.component.html',
	styleUrls: ['./order-edit.component.css']
})
export class OrderEditComponent implements OnInit {

	products;
	subscription;
	user;
	total: number;
	product_total: number;
	fileUrl = Config.FileUrl;
	submitted = false;
	love_code_value = "1577";
	carrier_type_value = "0";
	carrier_num_value;
	carrier_reg = false;
	countries = CountryList;
	invoice_name;
	vat_number;
	vat_reg = false;

	Form = this.formBuilder.group({
		name: ['',Validators.required],
		email: ['',[Validators.required, Validators.pattern('^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\\.[a-zA-Z0-9_-]+)+$')]],
		phone: [''],
		country: ['',Validators.required],
		//address: ['',Validators.required],
		thirdparty: ['',Validators.required],
		invoice_type: ['donate',Validators.required],
		carrier_type: [''],
		carrier_num: [''],
		love_code: [''],
		comment: [''],
		invoice_name: [''],
		vat_number: [''],
	});

	// convenience getter for easy access to form fields
	get name() { return this.Form.get('name'); }
	get email() { return this.Form.get('email'); }
	get phone() { return this.Form.get('phone'); }
	get country() { return this.Form.get('country'); }
	//get address() { return this.Form.get('address'); }
	get thirdparty() { return this.Form.get('thirdparty'); }
	get invoice_type() { return this.Form.get('invoice_type'); }

	constructor(private dialog:MatDialog, public productService:ProductService,  private formBuilder:FormBuilder, private memberService:MemberService, private httpService:HttpService, private router:Router, private route:ActivatedRoute) { }

	ngOnInit() {
		

		//console.warn(this.countries$);
		this.memberService.getUserInfo().subscribe(data => this.user = data);

		//日記訂閱
		if(this.router.url=="/subscription_edit"){
			this.getSubscription();

		//商品購物
		}else{
			this.getCart();
		}
		

	}


	//html內容讀取完改變時
	ngAfterContentChecked()  {

		if(this.products){

			//計算商品金額總和小計
		    this.product_total = Object.keys(this.products).reduce(
		    	(runningValue: number, key)=> {
		      		return runningValue = runningValue + (this.products[key].price * this.products[key].quantity);
		    	}, 0);

		    //計算訂單總計
		    ////有優惠卷
		    if(this.productService.discount_info){
		    	this.total = this.product_total - this.productService.discount_info.discount_amount;
		    }else{
		    	this.total = this.product_total;
		    }
		}
	}


	//取得訂閱資訊
	getSubscription(){
		let url = `${Config.APIUrl}/api/member/subscription/info`;
		this.httpService.getHttp(url).subscribe(
			res => {
				this.subscription = res;

			}
		);
	}


	//取得購物車內容
	getCart(){
		let url = `${Config.APIUrl}/api/shop/cart`;
		this.httpService.getHttp(url).subscribe(
			res => {
				this.products = res.products;

			}
		);
	}


	//檢查庫存
	checkQuantity(quantity, product_quantity){
		
		if(Number(quantity) > Number(product_quantity))	{
			return true;
		}
		else{
			return false;
		}
	}


	//submit form
	onSubmit() {

		/*驗證*/

		//判斷是否已按了送出 按了就要顯示驗證訊息
		this.submitted = true;

		//判斷電子發票資訊
		if(this.Form.value.invoice_type=='e-invoice'){
			console.log('ee');
			if(!this.Form.value.carrier_type || !this.Form.value.carrier_num || !this.carrier_reg)
				return;
			
		//判斷捐贈資訊
		}else if(this.Form.value.invoice_type=='donate'){
			console.log(this.Form.value);
			if(!this.Form.value.love_code)
				return;
			
		//判斷三聯資訊
		}else if(this.Form.value.invoice_type=='three'){
			if(!this.Form.value.invoice_name || !this.Form.value.vat_number || !this.vat_reg)
				return;
		}

		//表單沒有驗證成功 沒有動作
		if(this.Form.invalid){
			return;
		}
		/*驗證*/


		/* 送出*/

		//日記訂閱
		if(this.router.url=="/subscription_edit"){
			this.sendSubscription();

		//商品購物
		}else{
			this.sendProductOrder();
		}

		
	}


	//送出日記訂閱
	sendSubscription(){
		console.log(this.Form.value);
  		let params:FormData = new FormData();
  		Object.keys(this.Form.value).map(key=> params.append(key,this.Form.value[key]));

  		const url = `${Config.APIUrl}/api/member/subscription/register`;

	  	this.httpService.postHttp(url,params).subscribe(
	  		res => {
	  			console.log(res);
	  			swal({
						title: '操作成功',
						text: '前往付款',
						type: 'success',
					}).then(
						(success)=> {
							let payUrl = `${Config.APIUrl}/api/shop/period-payment/go-pay`;
				    		//this.router.navigate(['/member/subscription']);
				    		window.location.href = payUrl+'?id='+res.subscription_id+'&subscription_number='+res.subscription_number;
				    		
					});
	  		}
	  	);
	}


	//送出商品訂單
	sendProductOrder(){
		console.log(this.Form.value);
  		let params:FormData = new FormData();
  		Object.keys(this.Form.value).map(key=> params.append(key,this.Form.value[key]));

  		//新增折扣碼
  		if(this.productService.discount_code)
  			params.append('discount_code', this.productService.discount_code);

  		const url = `${Config.APIUrl}/api/shop/checkout/shop`;

	  	this.httpService.postHttp(url,params)
		    .subscribe(res => {
		    	console.log(res);
		    	switch (res.success_code) {
		    		case "EMPTY_CART":
		    			swal({
							title: '購物車內無商品',
							text: '請先選購商品再送出訂單',
							type: 'error',
						}).then(
							(success)=> {
					    		this.router.navigate(['/']);
						});
		    			break;

		    		case "OUT_OF_STOCK":
		    			swal({
							title: '選購商品庫存不足',
							text: '請回購物車修改商品內容',
							type: 'error',
						}).then(
							(success)=> {
					    		this.router.navigate(['../cart'], { relativeTo: this.route });
						});
		    			break;

		    		case "STATUS_ERROR":
		    			swal({
							title: '選購商品已下架',
							text: '請回購物車修改商品內容',
							type: 'error',
						}).then(
							(success)=> {
					    		this.router.navigate(['../cart'], { relativeTo: this.route });
						});
		    			break;

		    		case "COUPON_INVALID":
		    			swal({
							title: '優惠碼無效',
							text: '請回購物車修改優惠碼資訊',
							type: 'error',
						}).then(
							(success)=> {
					    		this.router.navigate(['../cart'], { relativeTo: this.route });
						});
		    			break;
		    		
		    		//成功
		    		default:
		    			let payUrl = `${Config.APIUrl}/api/shop/payment/go-pay`;

		    			swal({
							title: '操作成功',
							text: '前往付款',
							type: 'success',
						}).then(
							(success)=> {
								let payUrl = `${Config.APIUrl}/api/shop/payment/go-pay`;
					    		//this.router.navigate(['/member/order-record']);
					    		window.location.href = payUrl+'?id='+res.id+'&order_number='+res.order_number;
						});
		    		
		    			//console.log(res.id);
		    			break;
		    	}
		    	
		    });
	}


	//統一編號檢測
	isVatnumber(){
		let regexp = /^\d{8}$/;
		this.vat_reg = regexp.test(this.vat_number);
	}


	//自然人憑證或手機條碼檢測
	isCertificated(type) {

		//自然人憑證
		if(type=="1"){
			let regexp = /^[a-zA-Z]{2}[0-9]{14}$/;
			this.carrier_reg = regexp.test(this.carrier_num_value);

		//手機條碼
		}else if(type=="0"){
			let regexp = /^\/{1}[0-9A-Z]{7}$/;
			this.carrier_reg = regexp.test(this.carrier_num_value);
		}
		

		

		console.log(this.carrier_reg + this.carrier_num_value + type);
	}


	//開啟pop up
	openDialog(){
	  	const dialogRef = this.dialog.open(ThirdPartyComponent, {
	  		maxWidth: '100%',
		  	width: '572px',
			});
	}

}
