import { isPlatformBrowser , DOCUMENT} from '@angular/common';
import { environment } from '../environments/environment';
import { Component , OnInit, Inject, PLATFORM_ID} from '@angular/core';
import { HttpService } from "./services/http.service";
import { Spinkit } from 'ng-http-loader';
import { CookieService } from './services/cookie.service';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  	title = 'angular-template';

  	public spinkit = Spinkit;


  	constructor(@Inject(PLATFORM_ID) private platformId: any, @Inject(DOCUMENT) private document: any, public httpService:HttpService, private cookieService:CookieService) { }


	public ngOnInit(): void {
		if (isPlatformBrowser(this.platformId)) {
			let bases = this.document.getElementsByTagName('base');
	
			if (bases.length > 0) {
				bases[0].setAttribute('href', environment.baseHref);
			}
		}
	
		if (isPlatformBrowser(this.platformId)) {
			let bases = this.document.getElementsByTagName('base');

			if (bases.length > 0) {
				bases[0].setAttribute('href', environment.baseHref);
			}
		}


		//production 環境 close console.log();
		if (environment.production)
			this.cookieService.clearConsoleLog();
		
	}


}
