import { Component, OnInit } from '@angular/core';
import { LayoutService } from "../../services/layout.service";
import { Router, ActivatedRoute, ParamMap } from '@angular/router';

@Component({
	selector: 'app-psychology-home',
	templateUrl: './psychology-home.component.html',
	styleUrls: ['./psychology-home.component.css']
})
export class PsychologyHomeComponent implements OnInit {

	psychology_param = {
			category_id: 0,
			sort: 'post_date',
			sort_order: 'desc',
			from: 0,
		};
	category_id = 0;
	input_category_ids = {
		'parent_id' : 0,
		'category_id' : 0,
	};

	constructor(public layoutService:LayoutService, private route:ActivatedRoute) { }

	ngOnInit() {
		this.layoutService.getSummary('page_test_introduction');
		this.layoutService.setMetaId(5);
		
		this.route.params.subscribe(params=>{
			if(params['id']){
				this.category_id = params['id'];
				this.input_category_ids = {
					'parent_id' : params['parentId'],
					'category_id' : params['id'],
				};
			}
		});
		this.uiControl();
	}


	//sort
	sort($type,$order){
		this.psychology_param = {
			category_id: this.category_id,
			sort: $type,
			sort_order: $order,
			from: 0,
		};
	}


	//category component output
	getCategoryId($id){
		this.category_id = $id;
		this.psychology_param = {
			category_id: $id,
			sort: 'post_date',
			sort_order: 'desc',
			from: 0,
		};
		//location.reload();
	}


  // ui jquery
	uiControl(){
		// 類別篩選器
		$('.toggle-category').click(function(){
			$(this).children('[data-icon]').toggle();
			$('.dropdown-category').toggle();
		});
	}

}


