	//取得首頁各類別的清單
	getList(zone, url){
		url = `${Config.APIUrl}${url}`;
		this.httpService.getHttp(url).subscribe(
			res => {
				Object.keys(res).map(key => this.list[zone] = res[key]);
				console.log(this.list);
			}
		);

	}


  //取得首頁內容
  getHome(){
    let url = `${Config.APIUrl}/api/home-page?website_id=1`;
    this.httpService.getHttp(url).subscribe(
      res => {
        this.list = res;
      }
    );
  }

