import { Component, OnInit, Input, SimpleChanges } from '@angular/core';
import { HttpService } from '../../services/http.service';
import  * as Config from '../../models/config.model';


@Component({
	selector: 'app-hashtag-test-answer',
	templateUrl: './hashtag-test-answer.component.html',
	styleUrls: ['./hashtag-test-answer.component.scss']
})
export class HashtagTestAnswerComponent implements OnInit {

	//input 顯示序號
	@Input() input_param;


	//如果input進來的參數改變, 重新取得心理測驗列表
	ngOnChanges(changes: SimpleChanges) {
		console.log(changes);
    	this.input_param = changes.input_param.currentValue;
    	this.readmoreCheck = 0;

    	this.getPsychologyAnswer();
	}

	
	list;
	fileUrl = Config.FileUrl;
	//是否開啟read more div
	readmoreCheck = 0;
	//一次要顯示幾筆
	limit = 8;
	moredata = 1;
	nodata = 0;

	constructor(private httpService:HttpService) { }

	ngOnInit() {
		this.readmoreCheck = 0;
		this.getPsychologyAnswer();
	}

	//取得心理測驗答案
	getPsychologyAnswer() {
		let url:string;
		const paramObject = {
				'keyword': this.input_param.keyword,
				'from': this.input_param.from,
				'limit':this.limit,
			};
		let param = this.httpService.getParamFormat(paramObject);

		url = `${Config.APIUrl}/api/member/psychological-test-answer/hashtag?${param}`;
		this.httpService.getHttp(url).subscribe(
			res => {

				//沒有資料 隱藏more data區塊
				if(res.answers.length==0){
					console.log("empty array");
					this.moredata = 0;
					this.nodata = 1;
				}

				this.list = res.answers;
				console.log(res);
			}
		);

	}

	//看更多
	more() {
		this.readmoreCheck = 1;
		this.input_param.from+=this.limit;	
	}

}
